<template>
  <v-form
    ref="form"
    v-model="validation.state"
  >
    <v-container>
      <!-- ДИАЛОГ СПРАВКИ -->
      <v-dialog
        v-model="infoDialog"
        width="700"
        scrollable
      >
        <v-card>
          <v-card-title class="headline">
            {{ $t('common.howToGet') }}
          </v-card-title>

          <v-card-text v-html="$t('adsmanager.accounts.add.howToGetBmToken')" />

          <v-divider />

          <v-card-actions>
            <v-spacer />
            <v-btn
              color="primary"
              text
              @click="infoDialog = false;"
            >
              {{ $t('common.close') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    
      <v-row>
        <v-col
          cols="12"
          sm="6"
        >
          <!-- НАЗВАНИЕ -->
          <v-col
            cols="12"
          >
            <v-text-field
              v-model="newAccount.name"
              :label="$t('common.name')"
              :rules="validation.rules.name"
              required
              outlined
              dense
            />
          </v-col>

          <!-- ТЕГИ -->
          <v-col
            cols="12"
          >
            <v-combobox
              v-model="newAccount.tags"
              :label="$t('common.tags')"
              :items="tags.all"
              multiple
              chips
              deletable-chips
              outlined
              :dense="!newAccount.tags || newAccount.tags && newAccount.tags.length === 0 ? true : false"
            />
          </v-col>

          <!-- ACCESS_TOKEN -->
          <v-col cols="12">
            <v-textarea
              v-model="newAccount.access_token"
              :label="$t('common.access_token')"
              :rules="validation.rules.access_token"
              outlined
              dense
              :spellcheck="false"
              @change="accessTokenCheck"
            />
            <v-alert
              v-if="showAccessTokenAlert"
              text
              color="red"
              dense
              hide-details
            >
              {{ $t('adsmanager.accounts.add.incorrectAccessTokenAlert') }}
            </v-alert>
          </v-col>

          <v-col cols="12">
            <v-expansion-panels accordion>
              <v-expansion-panel>
                <v-expansion-panel-header>{{ $t('upload.wizard.cardsTitles.additionally') }}</v-expansion-panel-header>
                <v-expansion-panel-content>
                  <!-- ТОКЕН БМА -->
                  <v-col cols="12">
                    <v-textarea
                      v-model="newAccount.business_access_token"
                      :label="`${$t('adsmanager.accounts.add.bmAccessToken')} (${$t('common.optional').toLowerCase()})`"
                      outlined
                      dense
                      hide-details
                      :spellcheck="false"
                      class="textarea-with-info-button"
                      @change="bmAccessTokenCheck"
                    >
                      <!-- СПРАВКА -->
                      <template #append>
                        <v-tooltip bottom>
                          <template v-slot:activator="{ on }">
                            <v-btn
                              x-small
                              icon
                              v-on="on"
                            >
                              <v-icon :size="16">
                                far fa-question-circle
                              </v-icon>
                            </v-btn>
                          </template>
                          <span>{{ $t('adsmanager.accounts.add.infoBmToken') }}</span>
                        </v-tooltip>
                      </template>
                    </v-textarea>

                    <!-- ПОЛУЧЕНИЕ ТОКЕНА БМ ЧЕРЕЗ COOKIES -->
                    <div>
                      <a
                        v-if="accountHasCookies"
                        href="#"
                        @click="getBmTokenWithCookies"
                      >
                        {{ $t('adsmanager.accounts.getBmTokenWithCookies') }}
                      </a>

                      <v-icon
                        v-if="gettingBmTokenWithCookies.progress"
                        :size="14"
                      >
                        fas fa-circle-notch fa-spin
                      </v-icon>

                      <v-icon
                        v-if="gettingBmTokenWithCookies.response && !gettingBmTokenWithCookies.result"
                        :size="16"
                        color="red"
                      >
                        fas fa-times-circle
                      </v-icon>
              
                      <v-icon
                        v-if="gettingBmTokenWithCookies.response && gettingBmTokenWithCookies.result"
                        :size="16"
                        color="green"
                      >
                        fas fa-check-square
                      </v-icon>
                    </div>

                    <v-alert
                      v-if="showBmAccessTokenAlert"
                      text
                      color="red"
                      dense
                      hide-details
                      class="my-2"
                    >
                      {{ $t('adsmanager.accounts.add.incorrectBmAccessTokenAlert') }}
                    </v-alert>

                    <div
                      class="span-link"
                      @click="infoDialog = true"
                    >
                      {{ $t('common.howToGet') }}
                    </div>
                  </v-col>

                  <!-- COOKIES -->
                  <v-col cols="12">
                    <v-textarea
                      :value="accountHasCookies ? JSON.stringify(account.cookies) : null"
                      :label="`${$t('common.cookies')} (${$t('common.optional').toLowerCase()})`"
                      outlined
                      dense
                      hide-details
                      :spellcheck="false"
                      class="textarea-with-info-button"
                      @input="cookiesInput = $event"
                    >
                      <!-- СПРАВКА -->
                      <template #append>
                        <v-tooltip bottom>
                          <template v-slot:activator="{ on }">
                            <v-btn
                              x-small
                              icon
                              v-on="on"
                            >
                              <v-icon :size="16">
                                far fa-question-circle
                              </v-icon>
                            </v-btn>
                          </template>
                          <span>{{ $t('adsmanager.accounts.add.infoCookies') }}</span>
                        </v-tooltip>
                      </template>
                    </v-textarea>
                    <v-alert
                      v-show="noFbCookies"
                      color="error"
                      dark
                      text
                      class="mt-2 mb-0"
                    >
                      {{ $t('adsmanager.accounts.noFbCookies') }}
                    </v-alert>
                  </v-col>

                  <!-- LSD -->
                  <!-- <v-col cols="12">
                    <v-textarea
                      :value="newAccount.lsd"
                      rows="1"
                      auto-grow
                      label="LSD"
                      outlined
                      dense
                      :spellcheck="false"
                      @input="sanitizeAdditionalTokens($event, 'lsd')"
                    >
                      <template #append>
                        <v-btn
                          x-small
                          icon
                          @click="$store.dispatch('accounts_v2/openDialog','additionalTokensInfo')"
                        >
                          <v-icon :size="16">
                            far fa-question-circle
                          </v-icon>
                        </v-btn>
                      </template>
                    </v-textarea>
                  </v-col> -->

                  <!-- FBDTSG -->
                  <!-- <v-col cols="12">
                    <v-textarea
                      :value="newAccount.fbdtsg"
                      rows="1"
                      auto-grow
                      label="FBDTSG"
                      outlined
                      dense
                      :spellcheck="false"
                      @input="sanitizeAdditionalTokens($event, 'fbdtsg')"
                    >
                      <template #append>
                        <v-btn
                          x-small
                          icon
                          @click="$store.dispatch('accounts_v2/openDialog','additionalTokensInfo')"
                        >
                          <v-icon :size="16">
                            far fa-question-circle
                          </v-icon>
                        </v-btn>
                      </template>
                    </v-textarea>
                  </v-col> -->

                  <!-- ПАРОЛЬ -->
                  <v-col cols="12">
                    <v-text-field
                      v-model="newAccount.password"
                      :label="`Facebook ${$t('common.password')}`"
                      outlined
                      dense
                      hide-details
                      :spellcheck="false"
                    />
                  </v-col>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-col>
        </v-col>

        <!-- ПРАВЫЙ СТОЛБЕЦ -->
        <v-col
          cols="12"
          sm="6"
        >
          <!-- USEAGENT -->
          <v-col
            cols="12"
          >
            <v-switch
              v-model="show.useragent"
              class="mt-0"
              :label="$t('common.useragent')"
            />

            <v-text-field
              v-if="show.useragent"
              v-model="newAccount.useragent"
              :label="$t('common.useragent')"
              :rules="validation.rules.useragent"
              outlined
              dense
            >
              <!-- КНОПКА РАНДОМИЗАЦИИ -->
              <template
                #prepend-inner
              >
                <v-btn
                  x-small
                  icon
                  @click="randomizeUseragent"
                >
                  <v-icon style="margin-top: 3px;">
                    autorenew
                  </v-icon>
                </v-btn>
              </template>
            </v-text-field>
          </v-col>

          <!-- ПРОКСИ -->
          <v-col
            cols="12"
          >
            <!-- ПОКАЗЫВАТЬ ПРОКСИ -->
            <v-switch
              v-model="show.proxy"
              :label="$t('common.proxy')"
            />
            <v-row v-if="show.proxy">
              <!-- СУЩЕСТВУЮЩИЙ ПРОКСИ -->
              <v-col
                cols="12"
                md="6"
              >
                <v-autocomplete
                  v-model="existingProxy"
                  :items="proxy.all.concat(newProxies)"
                  :label="$t('common.existingProxy')"
                  :item-value="item => item.id"
                  :filter="searchExistingProxy"
                  clearable
                  outlined
                  dense
                >
                  <template v-slot:item="{item}">
                    [{{ item.accounts_count }}]
                    {{ typeof item !== 'undefined' &&
                      typeof item.name !== 'undefined' && item.name !== null ?
                        item.name :
                        item.login === null ?
                          `${item.ip}:${item.port}` :
                          `${item.ip}:${item.port}:${item.login}:${item.password}`
                    }}
                  </template> 
                  <template v-slot:selection="{item}">
                    [{{ item.accounts_count }}]
                    {{ typeof item !== 'undefined' &&
                      typeof item.name !== 'undefined' && item.name !== null ?
                        item.name :
                        item.login === null ?
                          `${item.ip}:${item.port}` :
                          `${item.ip}:${item.port}:${item.login}:${item.password}`
                    }}
                  </template> 
                </v-autocomplete>
              </v-col>
              <!-- БЫСТРЫЙ ВВОД -->
              <v-col
                cols="12"
                md="6"
              >
                <v-text-field
                  v-model="quickInput"
                  :label="$t('adsmanager.accounts.add.quickInput')"
                  outlined
                  dense
                />
              </v-col>
              <!-- НАЗВАНИЕ ПРОКСИ -->
              <v-col
                cols="12"
                sm="6"
              >
                <v-text-field
                  v-if="newAccount.proxy"
                  v-model="newAccount.proxy.name"
                  :label="$t('common.name')"
                  outlined
                  dense
                />
              </v-col>
              <!-- ТИП ПРОКСИ -->
              <v-col
                cols="12"
                sm="6"
              >
                <v-select
                  v-if="newAccount.proxy"
                  v-model="newAccount.proxy.type"
                  :label="$t('common.type')"
                  :items="proxy_types"
                  :rules="validation.rules.proxy"
                  outlined
                  dense
                />
              </v-col>
              <!-- ХОСТ ПРОКСИ -->
              <v-col
                cols="12"
                sm="6"
              >
                <v-text-field
                  v-if="newAccount.proxy"
                  v-model="newAccount.proxy.ip"
                  :label="$t('common.host')"
                  :rules="validation.rules.proxy"
                  outlined
                  dense
                />
              </v-col>
              <!-- ПОРТ ПРОКСИ -->
              <v-col
                cols="12"
                sm="6"
              >
                <v-text-field
                  v-if="newAccount.proxy"
                  v-model="newAccount.proxy.port"
                  :label="$t('common.port')"
                  :rules="validation.rules.proxy"
                  outlined
                  dense
                />
              </v-col>
              <!-- ЛОГИН ПРОКСИ -->
              <v-col
                cols="12"
                sm="6"
              >
                <v-text-field
                  v-if="newAccount.proxy"
                  v-model="newAccount.proxy.login"
                  :label="$t('common.login')"
                  outlined
                  dense
                />
              </v-col>
              <!-- ПАРОЛЬ ПРОКСИ -->
              <v-col
                cols="12"
                sm="6"
              >
                <v-text-field
                  v-if="newAccount.proxy"
                  v-model="newAccount.proxy.password"
                  :label="$t('common.password')"
                  outlined
                  dense
                />
              </v-col>
            </v-row>
          </v-col>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import {mapGetters} from 'vuex';

import proxy_types  from '../../../constants/proxy/proxy_types';
import useragents   from '../../../constants/useragents';

export default {
  name: 'AccountsDialogForm',

  props: {
    edit: {
      type: Boolean,
      default: false,
    },

    account: {
      type: Object,
      default: () => ({})
    }
  },

  data: function() {
    return ({
      proxy_types,
      useragents,
      validation: {
        state: false,
        rules: {
          name: [
            v => !!v || this.$t('common.required').toLowerCase()
          ],
          access_token: [
            v => !!v || this.$t('common.required').toLowerCase()
          ],
          proxy: [
            (v) => {
              if (v && v.length === 0 && this.show.proxy) return this.$t('common.required').toLowerCase();
              return true;
            } 
          ],
          useragent: [
            (v) => {
              if (v && v.length === 0 && this.show.useragent) return this.$t('common.required').toLowerCase();
              return true;
            } 
          ]
        }
      },
      existingProxy: {},
      newAccount: {
        name: '',
        tags: [],
        access_token: '',
        business_access_token: '',
        useragent: '',
        proxy: {
          name: '',
          type: '',
          host: '',
          port: '',
          login: '',
          password: '',
        },
        newProxy: null,
        cookies:   null,
        lsd: '',
        fbdtsg: '',
        password: ''
      },
      show: {
        useragent: true,
        proxy: false,
      },
      quickInput: null,
      newProxies: [],
      showAccessTokenAlert: false,
      showBmAccessTokenAlert: false,
      cookiesInput: '',
      infoDialog: false,
      gettingBmTokenWithCookies: {
        progress: false,
        response: null,
        result: false,
      },
      noFbCookies: false
    });
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      tags: 'tags/tags',
      proxy: 'proxy/proxy'
    }),

    accountHasCookies() {
      return (
        typeof this.account !== 'undefined'
        && typeof this.account.cookies !== 'undefined'
        && Array.isArray(this.account.cookies)
        && this.account.cookies.length > 0
      );
    }
  },

  watch: {
    'show.useragent': function() {
      if (this.show.useragent === false) this.newAccount.useragent = '';
    },

    'show.proxy': function() {
      this.$emit('proxyChange', this.show.proxy);

      if (this.show.proxy === false) {
        this.newAccount.proxy = {
          name: '',
          type: '',
          ip: '',
          port: '',
          login: '',
          password: '',
        };
      } else {
        if (this.existingProxy) { 
          this.loadDataFromExistingProxy();
        }
      }
    },

    existingProxy: function() {
      this.loadDataFromExistingProxy();
    },

    newAccount: {
      deep: true,
      handler() {
        this.$emit('change', this.newAccount);
      }
    },

    'newAccount.useragent': {
      deep: true,
      handler(newVal) {
        this.$emit('useragentChange', newVal);
      }
    },

    'validation.state': function() {
      this.$emit('validation-changed', this.validation.state);
    },

    quickInput: function() {
      this.parse();
    },
    
    cookiesInput: {
      handler (newVal) {
        this.parseCookies(newVal);
      }
    },    
    'newAccount.cookies': {
      deep: true,
      handler(newVal) {
        this.noFbCookies = false;

        if (Array.isArray(newVal) && newVal.length) {
          const cookiesWithFb = newVal.filter((i) => {
            try {
              const keys = Object.keys(i);
              
              for (const key of keys) {
                if (key.toLowerCase() === 'domain' && i[key].includes('facebook.com')) {
                  return true;
                }
              }

              return false;
            } catch (error) {
              return false;
            }
          });
          if (cookiesWithFb.length === 0) {
            this.noFbCookies = true;
          }
        }
      }
    },
  },

  created() {
    this.randomizeUseragent();
    if (this.edit) {
      this.$store.dispatch('tags/loadTags');
      this.$store.dispatch('proxy/loadProxy');
      if (this.account.proxy_id !== null) {
        this.show.proxy = true;
        this.existingProxy = this.account.proxy_id;
      }

      this.newAccount = this.$_.cloneDeep(this.account);
      if (this.newAccount.user_agent !== null && this.newAccount.user_agent.length > 0) {
        this.show.useragent = true;
        this.newAccount.useragent = this.newAccount.user_agent;
      }
    }
  },

  methods: {
    async getBmTokenWithCookies() {
      this.$set(this.gettingBmTokenWithCookies, 'progress', true);
      
      let response;
      try {
        response = await this.api(`/accounts/get_business_token_with_cookies/?account_id=${this.account.id}`);
      } catch (err) {
        this.$set(this.gettingBmTokenWithCookies, 'progress', false);
      }
      
      this.$set(this.gettingBmTokenWithCookies, 'progress', false);

      this.$set(this.gettingBmTokenWithCookies, 'response', response.data);
      this.$set(this.gettingBmTokenWithCookies, 'result', response.data.result);
        
      if (
        response.data
        && response.data.result
        && response.data.token
      ) {
        this.$set(this.newAccount, 'business_access_token', response.data.token);
      }
    },

    searchExistingProxy(item, queryText) {
      const q = queryText.toLowerCase();
      if (item && item.name && item.name.toString().toLowerCase().search(q) !== -1) return true;
      if (item.ip && item.ip.toString().toLowerCase().search(q) !== -1) return true;
      if (item.port && item.port.toString().toLowerCase().search(q) !== -1) return true;
      if (item.login && item.login.toString().toLowerCase().search(q) !== -1) return true;
      if (item.password && item.password.toString().toLowerCase().search(q) !== -1) return true;

      return false;
    },

    loadDataFromExistingProxy() {
      if (typeof this.existingProxy === 'undefined') {
        this.newAccount.proxy = {
          name: '',
          type: '',
          ip: '',
          port: '',
          login: '',
          password: '',
        };
        this.quickInput = null;
      }
      const proxy = this.proxy.all.find(p => p.id === this.existingProxy);
      if (proxy) {
        this.newAccount.proxy = {...proxy};
      } 
    },

    accessTokenCheck() {
      if(this.newAccount.access_token.toLowerCase().search('eaabs') !== 0) {
        return this.showAccessTokenAlert = true;
      }
      return this.showAccessTokenAlert = false;
    },

    bmAccessTokenCheck() {
      if(this.newAccount.business_access_token.toLowerCase().startsWith('eaag')) {
        return this.showBmAccessTokenAlert = false;
      }
      return this.showBmAccessTokenAlert = true;
    },

    parse() {
      if (this.quickInput === null || this.quickInput === '') return;
      this.newProxies = [];
      let data = {
        proxy: {
          id: null,
          ip: null,
          port: null,
          login: null,
          password: null,
          name: null,
          accounts_count: 0,
          type: null
        },
        newProxy: null,
      };
      let proxy = this.quickInput.trim();
      if (proxy.indexOf(':') === -1) {
        let existingProxy = this.proxy.all.find(p => p.id === parseInt(proxy));
        data.proxy = {...existingProxy};
        data.newProxy = null;
      } else {
        data.newProxy = this.quickInput.trim();
        let proxyType = null;
        if (proxy.indexOf('/') === -1) {
          proxyType = 'http';
        } else {
          if (proxy.split('://').length > 1) {
            proxyType = proxy.split('://')[0].toLowerCase();
            if (proxyType === 'https') proxyType = 'http';
            if (proxyType === 'socks') proxyType = 'socks5';
            proxy = proxy.split('://')[1];
          }

        }
        if (proxy.split(':').length > 1) {
          if (proxy.split(':').length === 2) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: null,
              password: null,
              name: null,
              accounts_count: 0,
              type: proxyType,
            };
          }
          if (proxy.split(':').length === 3) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: null,
              password: null,
              name: proxy.split(':')[2].trim(),
              accounts_count: 0,
              type: proxyType,
            };
          }
          if (proxy.split(':').length === 4) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: proxy.split(':')[2].trim(),
              password: proxy.split(':')[3].trim(),
              name: null,
              accounts_count: 0,
              type: proxyType,
            };
          }
          if (proxy.split(':').length === 5) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: proxy.split(':')[2].trim(),
              password: proxy.split(':')[3].trim(),
              name: proxy.split(':')[4].trim(),
              accounts_count: 0,
              type: proxyType,
            };
          }
        }
      }
      if (data.proxy.id === null && data.proxy.ip !== null && data.proxy.port !== null) {
        this.newProxies.push({...data.proxy});
        this.existingProxy = {...data.proxy};
        this.newAccount.proxy = {...data.proxy};
      }
    },

    randomizeUseragent() {
      let max = this.useragents.length;
      let min = 1;
      let randomUseragent = Math.floor(Math.random() * (max - min + 1) + min);
      return this.newAccount.useragent = this.useragents[randomUseragent - 1];
    },

    isJsonString (str) {
      try {
        JSON.parse(str);
      } catch (e) {
        return false;
      }
      return true;
    },

    isBase64 (str) {
      try {
        return btoa(atob(str)) === str;
      } catch (err) {
        return false;
      }
    },

    parseNetscapeCookies (data) {
      const lines = data.toString().split('\n');
      if (lines.length === 0) return [];

      const result = [];

      for (const _line of lines) {
        const line = _line.toString();

        // Пропускаем
        //   - комментарии
        //   - пустые строки
        //   - строки без символа табуляции
        if (
          line.substring(0, 1) === '#' ||
            line.trim().length === 0 ||
            line.search('\t') === -1
        ) continue;

        const parts = line.split('\t');
        const row = {};

        row.domain = typeof parts[0] === 'undefined'
          ? null
          : parts[0];
        row.httpOnly = typeof parts[1] === 'undefined'
          ? null
          : parts[1].toString().toUpperCase() === 'TRUE';
        row.path = typeof parts[2] === 'undefined'
          ? null
          : parts[2];
        row.secure = typeof parts[3] === 'undefined'
          ? null
          : parts[3].toString().toUpperCase() === 'TRUE';
        row.expirationDate = typeof parts[4] === 'undefined'
          ? null
          : parseInt(parts[4]);
        row.name = typeof parts[5] === 'undefined'
          ? null
          : parts[5];
        row.value = typeof parts[6] === 'undefined'
          ? null
          : parts[6];

        result.push(row);
      }

      return result;
    },

    parseCookies (cookiesInput) {
      let cookiesString = cookiesInput;
      this.newAccount.cookies = '';

      if (this.isBase64(cookiesString)) {
        cookiesString = atob(cookiesString);
      }

      if (this.isJsonString(cookiesString)) {
        this.newAccount.cookies = JSON.parse(cookiesString);
        return;
      } else {
        // ЗДЕСЬ КОГДА-НИБУДЬ БУДЕТ ПАРСИНГ КУКОВ ИЗ ЗАГОЛОВКОВ
      }
      this.newAccount.cookies = this.parseNetscapeCookies(cookiesString);
    },

    sanitizeAdditionalTokens(value, type) {
      if (type === 'lsd') {
        this.$set(
          this.newAccount, 
          'lsd', 
          value
            .split(' ').join('')
            .split('"').join('')
            .split('\n').join('')
            .replace('lsd', '')
            .replace('LSD', '')
        );
      }
      if (type === 'fbdtsg') {
        this.$set(
          this.newAccount, 
          'fbdtsg', 
          value
            .split(' ').join('')
            .split('"').join('')
            .split('\n').join('')
            .replace('fbdtsg', '')
            .replace('FBDTSG', '')
        );
      }
    }
  }
};
</script>

