<template>
  <!-- ИНДИКАТОР ЗАГРУЗКИ -->
  <v-dialog
    :value="dialogs.unarchive"
    hide-overlay
    persistent
    width="300"
    content-class="loading-modal"
  >
    <v-card
      color="primary"
    >
      <v-card-text>
        <div style="display: flex; justify-content: space-between;">
          <div>
            {{ $t('dialogs.accounts.unarchive.loadingText') }}
          </div>
          <div>
            {{ done }} / {{ accounts.selected.length }}
          </div>
        </div>
          
        <v-progress-linear
          indeterminate
          color="white"
          class="mb-0"
        />
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'AccountsDialogsUnarchive',

  data() {
    return {
      showLoading: true,
      done: 0,
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/accounts',
      dialogs: 'accounts_v2/dialogs'
    })
  },

  async created() {
    if (this.accounts.selected && Array.isArray(this.accounts.selected) && this.accounts.selected.length > 0) {
      const data = {
        ids: this.accounts.selected.map(account => account.id)
      };
      await this.api.post('/accounts/unarchive', data)
        .catch(() => true);
      this.$store.dispatch('accounts_v2/closeDialog', 'unarchive');
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
    } else {
      this.$store.dispatch('accounts_v2/closeDialog', 'unarchive');
    }
  }
};
</script>