<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          id="adsmanagerMainTableAccounts"
          :value="selected"
          :headers="cols"
          :items="accounts.filtered"
          :loading="loading.mainTable"
          :loading-text="$t('common.loading')"
          fixed-header
          :height="$vuetify.breakpoint.xs ? innerHeight - 166 : $vuetify.breakpoint.sm ? innerHeight - 166 : innerHeight - 50 - 48 - 32"
          show-select
          class="stat-table stat-table-accounts"
          :custom-sort="customSortsAccounts"
          :server-items-length="accounts.count"
          hide-default-footer
          :mobile-breakpoint="0"
          :header-props="{'sort-icon': 'arrow_downward' }"
          :page.sync="page"
          :options.sync="options"
          :items-per-page="itemsPerPage"
          @toggle-select-all="selectAll"
          @item-selected="selectItem"
          @update:items-per-page="setItemsPerPage($event)"
          @update:page="updatePage"
        >
          <template #[`header.data-table-select`]="{ on , props }">
            <v-simple-checkbox
              color="primary"
              v-bind="props"
              style="width: 48px; text-align: center;"
              v-on="on"
            />
          </template>
          <template #body="{items}">
            <tbody>
              <tr
                v-for="item in items"
                :key="`row-${item.id}`"
                style="cursor: pointer;"
                @click="selectItemByRow(item, $event)"
              >
                <td>
                  <div>
                    <v-simple-checkbox
                      v-ripple
                      color="primary"
                      :value="accounts.selected.find(account => account.id === item.id) !== undefined"
                      style="width: 48px; text-align: center;"
                      @input="selectItem(item, $event)"
                    />
                  </div>
                </td>
                <td>
                  <accounts-main-table-info :account="item" />
                </td>
                <td>
                  <accounts-main-table-status :account="item" />
                </td>
                <template v-for="col in profile.columns">
                  <simple-stat-cell
                    v-if="showSimpleStatCell(col)"
                    :key="`stat-col-${col}`"
                    :col="col"
                    :item="item"
                  />
                  <complex-stat-cell-quantity
                    v-if="actionCols.indexOf(col) > -1"
                    :key="`stat-col-${col}-quantity`"
                    :col="col"
                    :item="item"
                  />
                  <complex-stat-cell-cr
                    v-if="col.lastIndexOf('cr') === col.length - 2"
                    :key="`stat-col-${col}-cr`"
                    :col="col"
                    :item="item"
                  />
                  <complex-stat-cell-cpa
                    v-if="col.lastIndexOf('cpa') === col.length - 3"
                    :key="`stat-col-${col}-cpa`"
                    :col="col"
                    :item="item"
                  />
                  <account-fan-pages-cell
                    v-if="col === 'account_fan_pages'"
                    :key="`stat-col-${col}-cpa`"
                    :item="item"
                  />
                  <account-bms-cell
                    v-if="col === 'account_bms'"
                    :key="`stat-col-${col}-cpa`"
                    :item="item"
                  />
                </template>
              </tr>
            </tbody>
            <!-- TOTAL -->
            <tfoot>
              <tr v-if="accounts.filtered.length > 0">
                <td>&nbsp;</td>
                <td>&nbsp;</td>
                <td>&nbsp;</td>
                <template v-for="col in profile.columns">
                  <simple-stat-cell
                    v-if="showSimpleStatCell(col)"

                    :key="`stat-col-${col}-total`"
                    :col="col"
                    it-is-total-row
                  />
                  <complex-stat-cell-quantity
                    v-if="actionCols.indexOf(col) > -1"
                    :key="`stat-col-${col}-quantity-total`"
                    :col="col"
                    it-is-total-row
                  />
                  <complex-stat-cell-cr
                    v-if="col.lastIndexOf('cr') === col.length - 2"
                    :key="`stat-col-${col}-cr-total`"
                    :col="col"
                    it-is-total-row
                  />
                  <complex-stat-cell-cpa
                    v-if="col.lastIndexOf('cpa') === col.length - 3"
                    :key="`stat-col-${col}-cpa-total`"
                    :col="col"
                    it-is-total-row
                  />
                  <td
                    v-if="col === 'account_fan_pages'"
                    :key="`stat-col-${col}-total`"
                  >
                    &nbsp;
                  </td>
                  <td
                    v-if="col === 'account_bms'"
                    :key="`stat-col-${col}-total`"
                  >
                    &nbsp;
                  </td>
                </template>
              </tr>
            </tfoot>
          </template>
        </v-data-table>

        <!-- ФУТЕР ТАБЛИЦЫ -->
        <div
          class="d-flex justify-end align-center dolphin-main-table-footer"
          :class="{'justify-center': $vuetify.breakpoint.xs || $vuetify.breakpoint.sm, 'justify-space-between': !$vuetify.breakpoint.xs && !$vuetify.breakpoint.sm}"
        >
          <!-- ТЕГИ -->
          <div
            v-if="!$vuetify.breakpoint.xs && !$vuetify.breakpoint.sm"
            style="overflow: hidden; white-space: nowrap; overflow-x: auto; scroll-direction: horizontal; width: auto; margin-right: auto;"
            class="dolphin-main-table-footer-tags"
          >
            <v-chip
              v-for="tag in popularTags"
              :key="tag"
              class="mr-1"
              :color="detectColor(tag)"
              dark
              :outlined="filters.tags && !filters.tags.includes(tag)"
              x-small
              label
              @click="filterByTags(tag)"
            >
              {{ tag }}
            </v-chip>
          </div>

          <!-- ПАГИНАЦИЯ -->
          <div
            style="white-space: nowrap;"
            class="mx-2"
          >
            {{ currentTableItems + ' ' + (accounts.count ? $t('common.of').toLowerCase() + ' ' + accounts.count : '') }}
          </div>

          <div style="white-space: nowrap;">
            <!-- СТРЕЛКА ВЛЕВО -->
            <v-btn
              icon
              x-small
              :disabled="page === 1"
              @click="changePage(false)"
            >
              <v-icon :size="12">
                fas fa-chevron-left
              </v-icon>
            </v-btn>

            <!-- СТРАНИЦА -->
            <v-chip
              label
              color="primary"
              small
              class="mx-2"
            >
              {{ page }}
            </v-chip>

            <!-- СТРЕЛКА ВПРАВО -->
            <v-btn
              icon
              x-small
              :disabled="page === pageCount"
              @click="changePage(true)"
            >
              <v-icon :size="12">
                fas fa-chevron-right
              </v-icon>
            </v-btn>
          </div>

          <v-select
            :value="itemsPerPage"
            :items="[10, 30, 50, 100, -1]"
            hide-details
            dense
            solo
            class="dolphin-main-table-footer-items-per-page ml-2"
            :class="{'dolphin-main-table-footer-items-per-page-mobile': $vuetify.breakpoint.sm || $vuetify.breakpoint.xs}"
            style="max-width: 60px;"
            @change="setItemsPerPage($event)"
          >
            <template #item="{item}">
              {{ item === -1 ? $t('common.all') : item }}
            </template>
            <template #selection="{item}">
              {{ item === -1 ? $t('common.all') : item }}
            </template>
          </v-select>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>

import mainTableColumnsResizeHandler                      from '@/mixins/mainTableColumnsResizeHandler.js';
import { mapGetters }                                     from 'vuex';

import actionCols                                         from '../../../constants/adsmanager/action_cols';
import commonCols                                         from '../../../constants/adsmanager/common_cols';
import specialCols                                        from '../../../constants/adsmanager/special_cols';
import customSort                                         from '../../../mixins/adsmanager/custom_sort_v2';
import makeCols                                           from '../../../mixins/adsmanager/make_cols';
import selectItems                                        from '../../../mixins/adsmanager/select_items';
import AccountBmsCell                                     from '../stat-cells/AdsManagerAccountBmsCell';
import AccountFanPagesCell                                from '../stat-cells/AdsManagerAccountFanPagesCell';
import ComplexStatCellCpa                                 from '../stat-cells/AdsManagerComplexStatCellCpaV2';
import ComplexStatCellCr                                  from '../stat-cells/AdsManagerComplexStatCellCrV2';
import ComplexStatCellQuantity                            from '../stat-cells/AdsManagerComplexStatCellQuantityV2';
import SimpleStatCell                                     from '../stat-cells/AdsManagerSimpleStatCellV2';

import AccountsMainTableInfo                              from './AccountsMainTableInfo';
import AccountsMainTableStatus                            from './AccountsMainTableStatus';

export default {
  components: {
    AccountsMainTableStatus,
    AccountsMainTableInfo,
    AccountFanPagesCell,
    AccountBmsCell,
    SimpleStatCell,
    ComplexStatCellQuantity,
    ComplexStatCellCr,
    ComplexStatCellCpa
  },

  mixins: [
    makeCols,
    customSort,
    selectItems,
    mainTableColumnsResizeHandler
  ],

  data: function() {
    return ({
      actionCols,
      commonCols,
      specialCols,
      itemsPerPage: 50,
      pageCount: 1,
      cols: [],
      options: {},
    });
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts/accounts',
      loading: 'accounts/loading',
      innerHeight: 'main/innerHeight',
      profile: 'main/profile',
      stat: 'accounts/stat',
      pageCount: 'accounts/count',
      page: 'accounts/page',
      selected: 'accounts/selected',
      filters: 'accounts/filters',
    }),
    popularTags() {
      const allTagsObject = {};
      for (let item of this.accounts.all) {
        if (!item.tags) continue;
        item.tags.forEach(tag => {
          if (typeof allTagsObject[tag] === 'undefined') {
            allTagsObject[tag] = 1;
          } else {
            allTagsObject[tag] += allTagsObject[tag];
          }
        });
      }

      const allTagsArray = [];
      for (let tag in allTagsObject) {
        allTagsArray.push([tag, allTagsObject[tag]]);
      }
      allTagsArray.sort(function(a, b) {
        return b[1] - a[1];
      });

      const allTags = allTagsArray.map(item => item[0]);
      return allTags;
    },

    currentTableItems() {
      if (this.itemsPerPage === -1) {
        return this.accounts.filtered.length;
      }
      if (this.page === 1) {
        return `1 - ${this.itemsPerPage}`;
      }
      if (this.page === this.pageCount) {
        return `${(this.page * this.itemsPerPage) - (this.itemsPerPage - 1)} - ${this.accounts.filtered.length}`;
      }
      return `${(this.page * this.itemsPerPage) - (this.itemsPerPage - 1)} - ${this.page * this.itemsPerPage}`;
    },

    nativeCols() {
      let cols = [];

      cols.push({
        text: this.$t('common.account'),
        value: 'a.id',
        width: 150
      });
      cols.push({
        text: this.$t('common.status'),
        value: 'a.status',
        width: 200
      });

      return cols;
    }
  },

  watch: {
    options: {
      async handler () {
        const {sortBy, sortDesc} = this.options;
        localStorage.setItem('adsmanager-sorting-index' , sortBy[0]);
        localStorage.setItem('adsmanager-sorting-is-desc', sortDesc[0]);
        await this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
        this.pageCount = localStorage.getItem('adsmanager-accounts-page-count') || 0;
      },
      deep: false,
    },
    profile: {
      deep: true,
      handler(newProfile, oldProfile) {
        if (JSON.stringify(newProfile.columns) !== JSON.stringify(oldProfile.columns)) {
          this.cols = this.makeCols(this.nativeCols);
        }
      }
    },

    // cols: {
    //   deep: true,
    //   handler(newCols, oldCols) {
    //     if (JSON.stringify(newCols) !== JSON.stringify(oldCols)) {
    //       setTimeout(() => {
    //         if (this.$vuetify.breakpoint.smAndUp) {
    //           this.resizbaleColumns('Accounts', 1);
    //         }
    //         this.setWidths('Accounts');
    //       }, 100);
    //     }
    //   }
    // },

    stat: {
      deep: true,
      handler() {
        if (this.stat) {
          this.customSort(this.accounts.filtered, [this.sortBy], [this.sortDesc]);
        }
      }
    },

    'accounts.filtered': {
      deep: true,
      handler() {
        setTimeout(() => {
          this.setWidths('Accounts');
        }, 100);
      }
    }
  },

  created() {
    this.filters.newVersion = true;
    this.cols = this.makeCols(this.nativeCols);
    localStorage.setItem('adsmanager-items-page', 1);

    if (localStorage.getItem('adsmanager-items-per-page-count')) {
      this.itemsPerPage =  parseInt(localStorage.getItem('adsmanager-items-per-page-count'));
    }
  },

  beforeDestroy() {
    this.filters.newVersion = false;
  },

  // mounted() {
  //   this.setWidths('Accounts');
  //   setTimeout(() => {
  //     if (this.$vuetify.breakpoint.smAndUp) {
  //       this.resizbaleColumns('Accounts', 1);
  //     }
  //     this.setWidths('Accounts');
  //   }, 100);
  // },

  methods: {
    detectColor(tag) {
      if (tag === 'no-updates' || tag === 'не-обновлять' || tag === 'без-комментов' || tag === 'no-comments') return 'red';
      return 'primary';
    },

    async setItemsPerPage(number) {
      setTimeout(() => {
        this.setWidths('Accounts');
      }, 100);

      localStorage.setItem('adsmanager-items-per-page-count', number);
      this.itemsPerPage = number;
      this.page = 1;

      await this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      this.pageCount = localStorage.getItem('adsmanager-accounts-page-count') || 0;
    },

    filterByTags(tag) {
      let tags = this.filters.tags.concat([]);
      if (tags.includes(tag)) {
        tags = tags.filter(item => item !== tag);
      } else {
        tags.push(tag);
      }

      const data = {
        filter: 'tags',
        data: tags
      };
      this.$store.dispatch('accounts_v2/setSpecificFilter', data);
    },

    async updatePage() {
      localStorage.setItem('adsmanager-items-page', this.page);
      await this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      this.pageCount = localStorage.getItem('adsmanager-accounts-page-count') || 0;
      setTimeout(() => {
        this.setWidths('Accounts');
      }, 100);
    },

    changePage(next) {
      let page = 1;
      if(next) {
        page = this.page = this.page + 1;
      } else {
        page = this.page = this.page - 1;
      }
      this.$store.dispatch('accounts/setPage', page);
      this.$store.dispatch('accounts/loadAccounts');
    },

    showSimpleStatCell (col) {
      return commonCols.indexOf(col) > -1 &&
        !['transactions_daterange', 'transactions_total'].includes(col) &&
        specialCols.indexOf(col) === -1;
    }
  }
};
</script>
