    position: sticky !important;
    position: -webkit-sticky !important;
    bottom: 0;
    height: 32px;
  }
  .theme--light .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td {
    background-color: #eeeeee !important;
  }
  .theme--dark .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td {
    background-color: #2a2a2a !important
  }
