  .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(1),
  .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(1) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 0;
    z-index: 3;
  }
  .application-mobile--xs .stat-table-accounts div.v-data-table__wrapper > table > tbody > tr > td:nth-child(2),
  .application-mobile--xs .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(2),
  .application-mobile--xs .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(2) {
    width: 30vw !important;
    min-width: 30vw !important;
  }

  .stat-table-accounts div.v-data-table__wrapper > table > tbody > tr > td:nth-child(2),
  .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(2),
  .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(2) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 48px;
    z-index: 3;
  }

  .stat-table-accounts div.v-data-table__wrapper > table > tbody > tr > td:nth-child(3),
  .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(3),
  .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(3) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 198px;
    z-index: 3;
  }

  .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(1),
  .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(2),
  .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(3),
  .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > th:nth-child(1),
  .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > th:nth-child(2),
  .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > th:nth-child(3) {
    z-index: 4 !important;
  }

  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th,
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tbody > tr > td,
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td {
    position: static !important;
    z-index: 3 !important;
    height: 32px !important;
  }

  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tbody > tr > td:nth-child(1),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(1),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(1),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tbody > tr > td:nth-child(2),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(2),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(2) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 0;
    z-index: 3;
  }
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td,
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 0;
    z-index: 4;
  }
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(1),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > tfoot > tr > td:nth-child(2),
  .application-mobile .stat-table-accounts div.v-data-table__wrapper > table > thead > tr > th:nth-child(2) {
    z-index: 4 !important;
  }
</style>
