<template>
  <v-dialog
    :value="dialogs.shareFanPage"
    fullscreen
    transition="dialog-bottom-transition"
    scrollable
    @keydown.esc="close"
  >
    <!-- ДИАЛОГ ОШИБКИ -->
    <v-dialog
      v-model="detailsDialog"
      width="700"
      scrollable
    >
      <v-card>
        <v-card-title class="headline">
          {{ $t('common.learnMore') }}
        </v-card-title>

        <v-card-text style="margin-top: 40px;">
          <div class="d-flex justify-end">
            <div style="position: fixed; margin-top: -40px;">
              <v-btn
                color="primary"
                text
                @click="copyToClipboard(JSON.stringify(forDetails))"
              >
                <v-icon
                  :size="14"
                  class="mr-2"
                >
                  fas fa-copy
                </v-icon>
                {{ $t('common.copy') }}
              </v-btn>
            </div>
          </div>
          <code class="mt-0">{{ JSON.stringify(forDetails, null, 2) }}</code>
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="detailsDialog = false;"
          >
            {{ $t('common.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-card>
      <!-- ХЕДЕР ДИАЛОГА -->
      <v-toolbar
        color="primary"
        flat
        max-height="64"
      >
        <!-- КНОПКА ЗАКРЫТЬ -->
        <v-btn
          icon
          @click="close"
        >
          <v-icon>fas fa-times</v-icon>
        </v-btn>

        <!-- ТАЙТЛ -->
        <v-toolbar-title>
          {{ $t('adsmanager.accounts.shareFanPage') }}
        </v-toolbar-title>

        <v-spacer />

        <!-- КНОПКИ ХЭДЕРА -->
        <v-toolbar-items>
          <v-btn
            v-if="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? false : true"
            text
            @click="close"
          >
            {{ $t('common.cancel') }}
          </v-btn>

          <v-btn
            text
            :disabled="!readyToShare"
            :loading="loading"
            @click="share"
          >
            {{ $t('adsmanager.cabs.share') }}
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-row>
          <v-col :cols="6">
            <!-- КОМУ ПОСЫЛАТЬ -->
            <h2 class="mb-2">
              {{ $t('adsmanager.accounts.whereToSend') }}
            </h2>
            <v-autocomplete
              v-model="selectedAccounts"
              :item-text="item => item.name"
              :item-value="item => item.id"
              :items="accountToShare"
              outlined
              dense
              :hint="$t('adsmanager.accounts.dialogs.shareFp.multipleAccounts')"
              persistent-hint
              multiple
              clearable
              class="mb-4"
              return-object
            >
              <template #item="{item: account}">
                <v-list-item-content>
                  <v-list-item-title class="d-flex">
                    <span style="font-size: 18px;">
                      {{ account.name }}
                    </span>

                    <v-chip
                      v-if="colors[account.status] !== undefined"
                      label
                      :color="colors[account.status]"
                      x-small
                      outlined
                      style="font-weight: bold; margin-left: auto;"
                    >
                      <span v-if="account.status === 'TOKEN_ERROR'">
                        ERROR
                      </span>
                      <span v-else>
                        {{ account.status }}
                      </span>
                    </v-chip>
                  </v-list-item-title>
                  
                  <v-list-item-subtitle v-if="!['active', 'activity_block'].includes(account.status.toLowerCase())">
                    {{ $t('common.errorMessages.accountIsNotActive') }}
                  </v-list-item-subtitle>

                  <!-- <v-list-item-subtitle v-if="!account.password">
                    {{ $t('common.errorMessages.accountHasNoPassword') }}
                  </v-list-item-subtitle> -->

                  <v-list-item-subtitle v-if="!account.cookies">
                    {{ $t('adsmanager.accounts.noCookies') }}
                  </v-list-item-subtitle>
                </v-list-item-content>
              </template>
            </v-autocomplete>

            <!-- ВЫБОР РОЛИ -->
            <h2 class="mb-2">
              {{ $t('adsmanager.accounts.whatRole') }}?
            </h2>
            <v-autocomplete
              v-model="role"
              :items="[0, 1, 2, 3, 4]"
              outlined
              dense
              class="mb-4"
            >
              <template #item="{item}">
                {{ $t('adsmanager.pages.roles.' + item) }}
              </template>

              <template #selection="{item}">
                {{ $t('adsmanager.pages.roles.' + item) }}
              </template>
            </v-autocomplete>

            <!-- КАКИЕ СТРАНИЦЫ ПОСЫЛАТЬ -->
            <h2 class="mb-2">
              {{ $t('adsmanager.accounts.whatToSend') }}
            </h2>
            <v-treeview
              v-model="selectedFanPages"
              open-all
              selectable
              dense
              selected-color="primary"
              :items="accountsFanPages"
              class="accounts-dialog-share-fan-page"
            >
              <template #prepend="{ item }">
                <v-avatar
                  v-if="item.ava"
                  :size="24"
                >
                  <img
                    :src="item.ava"
                    :alt="item.name"
                  >
                </v-avatar>
              </template>
            </v-treeview>
          </v-col>

          <v-col :cols="6">
            <v-progress-linear
              v-if="$_.isEmpty(responses) || loading"
              :indeterminate="loading"
              :value="0"
            />
            <div v-else>
              <div
                v-for="response in responses"
                :key="`accounts-dialog-share-fan-page-response-${response}`"
              >
                <v-avatar
                  v-if="response.fp.ava"
                  :size="24"
                >
                  <img
                    :src="response.fp.ava"
                    :alt="response.fp.name"
                  >
                </v-avatar>
                {{ response.fp.name }} &rarr; {{ response.accountName }}

                <span v-if="response.success">
                  <v-icon color="green">
                    check_box
                  </v-icon>
                </span>

                <span v-else-if="response.error">
                  <v-icon color="red">
                    cancel
                  </v-icon>
                  <span
                    style="font-size: 12px; cursor: pointer; color: #2196F3; text-decoration: underline;"
                    @click="openDetails(response.response)"
                  >{{ $t('common.learnMore') }}</span>
                </span>
              </div>
            </div>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import colors          from '@/constants/accounts/status-colors';
import clipboard       from '@/mixins/clipboard';
import {mapGetters}    from 'vuex';

export default {
  name: 'AccountsDialogShareFanPage',

  mixins: [clipboard],

  data() {
    return {
      colors,
      selectedFanPages: [],
      selectedAccounts: null,
      loading: false,
      responses: [],
      detailsDialog: false,
      forDetails: [],
      role: 0
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      accounts: 'accounts_v2/ACCOUNTS',
    }),

    readyToShare() {
      return Array.isArray(this.selectedFanPages) && this.selectedFanPages.length > 0 && this.selectedAccounts && Array.isArray(this.selectedAccounts) && this.selectedAccounts.length > 0;
    },

    accountToShare() {
      return this.accounts.all
        .map(account => {
          account = this.$_.cloneDeep(account);

          if (account.cookies && ['active', 'activity_block'].includes(account.status.toLowerCase()) ) {
            account.disabled = false;
          } else {
            account.disabled = true;
          }

          return account;
        })
        .filter(i => !this.accounts.selected.map(a => parseInt(a.id, 10)).includes(parseInt(i.id, 10)));
    },

    accountsFanPages() {
      return this.accounts.selected.map(account => {
        account = this.$_.cloneDeep(account);

        return {
          id: account.id,
          name: account.name,
          disabled: Array.isArray(account.pages) && account.pages.length > 0 ? false : true,
          children: account.pages ? account.pages.map(item => {
            item.id = `${account.id}-${item.page_id}`;
            return item;
          }) : []
        };
      });
    },

    allFps() {
      return this.accounts.selected.reduce((allFps, account) => {
        if (account.pages) {
          return allFps.concat(account.pages);
        }
      }, []);
    }
  },

  methods: {
    close() {
      this.$store.dispatch('accounts_v2/closeDialog', 'shareFanPage');
    },
    
    async share() {
      try {
        this.loading = true;
        this.responses = [];

        for (const ids of this.selectedFanPages) {
          const splittedIds = ids.split('-');
          const fp = this.allFps.find(i => parseInt(i.page_id, 10) === parseInt(splittedIds[1], 10));
          if (!fp) continue;

          for (const selectedAccount of this.selectedAccounts) {
            const response = await this.api.post(`/pages/share/?from_account_id=${splittedIds[0]}&page_id=${fp.page_id}&to_account_id=${selectedAccount.id}&role=${this.role}`);

            if (response.status === 200 && response.data && response.data.result && response.data.result) {
              this.responses.push({
                fp: fp,
                accountName: selectedAccount.name,
                success: true,
                error: false,
                response: null
              });
            } else {
              this.responses.push({
                fp: fp,
                accountName: selectedAccount.name,
                success: false,
                error: true,
                response: response
              });
            }
          }
        }
        
      } catch (_) {
        // Do nothing
      } finally {
        this.loading = false;
        this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      }
    },

    openDetails(response) {
      this.forDetails = response;
      this.detailsDialog = true;
    }
  }
};
</script>