<template>
  <v-dialog
    v-model="dialogs.mainInfo"
    max-width="600px"
  >
    <v-card>
      <v-card-title>
        <span class="headline">
          <span style="color: gray;">
            [{{ accounts.forDialogMainInfo.id }}]
          </span>

          <v-icon
            :size="16"
            style="cursor: pointer; margin-right: 4px; color: gray;"
            :title="$t('common.fbApiLog')"
            @click="loadLog(accounts.forDialogMainInfo)"
          >
            fas fa-book
          </v-icon>

          <span style="color: gray;">{{ accounts.forDialogMainInfo.name }}</span>
        </span>
      </v-card-title>
      <v-card-text>
        <div>
          <div
            v-if="!accounts.forDialogMainInfo.fb_id"
            style="width: 90%; margin-top: 4px;"
          >
            <v-alert
              text
              color="error"
              style="padding: 0 5px; font-size: 14px; line-height: 17px;"
            >
              {{ $t('adsmanager.accounts.noFacebookId') }} 
              <a
                style="font-size: 14px;"
                @click="$store.dispatch('accounts_v2/openDialog', 'noFacebookIdInfo')"
              >
                {{ $t('common.whatsThat') }}
              </a>
            </v-alert>
          </div>

          <div
            class="mb-2"
            style="font-size: 14px; color: gray;"
          >
            <span
              class="d-block"
              style="line-height: 20px;"
            >
              {{ $t('common.owner') }}:
              {{ 
                accounts.forDialogMainInfo.user 
                  ? accounts.forDialogMainInfo.user.display_name ? accounts.forDialogMainInfo.user.display_name : accounts.forDialogMainInfo.user.login
                  : '-'
              }}
            </span>

            <span
              class="d-block"
              style="line-height: 14px;"
            >
              {{ $t('adsmanager.accounts.rulesAccepted') }}:
              <v-icon
                v-if="accounts.forDialogMainInfo.rules_accepted"
                :size="12"
                color="success"
              >
                fas fa-check
              </v-icon>
              <v-icon
                v-else
                :size="12"
                color="error"
              >
                fas fa-times
              </v-icon>
            </span>
          </div>
          <div
            v-if="accounts.forDialogMainInfo.permissions"
            class="caption"
          >
            <a @click="showPermissions">
              {{ $t('adsmanager.accounts.shared').toLowerCase() }} ({{ accounts.forDialogMainInfo.permissions.length }})
            </a>
          </div>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="$store.dispatch('accounts_v2/closeDialog', 'mainInfo')"
        >
          {{ $t('common.close') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters}                     from 'vuex';

export default {
  name: 'AccountsDialogMainInfo',

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      accounts: 'accounts_v2/accounts'
    })
  },

  methods: {
    async loadLog(account) {
      this.$store.dispatch('fbApiLog/clearLog');
      this.$store.dispatch('fbApiLog/openDialog', 'logSidebar');
      this.$store.dispatch('fbApiLog/loadLog', [{object: 'account_id', id: account.id}]);
    },  

    async showPermissions() {
      await this.$store.dispatch('accounts_v2/setForPermissions', this.accounts.forDialogMainInfo);
      await this.$store.dispatch('accounts_v2/openDialog', 'permissions');
    }
  }
};
</script>