<template>
  <v-dialog
    :value="dialogs.adAccountsManagement"
    :width="800"
    transition="dialog-bottom-transition"
    scrollable
    @keydown.esc="closeDialog"
    @click:outside="closeDialog"
  >
    <!-- ДИАЛОГ ОШИБКИ -->
    <v-dialog
      v-model="detailsDialog"
      width="700"
      scrollable
    >
      <v-card>
        <v-card-title class="headline">
          {{ $t('common.learnMore') }}
        </v-card-title>

        <v-card-text style="margin-top: 40px;">
          <div class="d-flex justify-end">
            <div style="position: fixed; margin-top: -40px;">
              <v-btn
                color="primary"
                text
                @click="copyToClipboard(JSON.stringify(forDetails))"
              >
                <v-icon
                  :size="14"
                  class="mr-2"
                >
                  fas fa-copy
                </v-icon>
                {{ $t('common.copy') }}
              </v-btn>
            </div>
          </div>
          <code class="mt-0">{{ JSON.stringify(forDetails, null, 2) }}</code>
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="detailsDialog = false;"
          >
            {{ $t('common.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-card>
      <!-- ХЕДЕР ДИАЛОГА -->
      <v-toolbar
        color="primary"
        dark
        flat
        dense
      >
        <!-- КНОПКА ЗАКРЫТЬ -->
        <v-btn
          icon
          @click="closeDialog"
        >
          <v-icon>fas fa-times</v-icon>
        </v-btn>

        <!-- ТАЙТЛ -->
        <v-toolbar-title>
          {{ $t('adsmanager.accounts.adAccountsManagement') }}
        </v-toolbar-title>

        <v-spacer />

        <!-- КНОПКИ ХЭДЕРА -->
        <v-toolbar-items>
          <v-btn
            v-if="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? false : true"
            text
            @click="closeDialog"
          >
            {{ $t('common.cancel') }}
          </v-btn>
          <v-btn
            text
            :disabled="selectedCabs.length === 0"
            :loading="loading"
            @click="removeCabs"
          >
            {{ $t('common.leaveAdAccounts') }}
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      
      <v-card-text>
        <v-data-table
          :headers="colsAccounts"
          fixed-header
          :items="accounts.selected"
          class="accounts-dialog-ad-accounts-management-accounts-table"
        >
          <!-- НАЗВАНИЕ АККАУНТА -->
          <template #[`item.account`]="{item: account}">
            {{ account.name }}
          </template>

          <!-- КАБИНЕТ -->
          <template #[`item.adAccount`]="{item: account}">
            <v-data-table
              v-model="selectedCabs"
              :headers="colsCabs"
              hide-default-header
              hide-default-footer
              :items="cabsFromAccount(account)"
              :items-per-page="-1"
              show-select
              class="accounts-dialog-ad-accounts-management-cabs-table"
            >
              <template #[`item.data-table-select`]="{item: cab}">
                <v-checkbox
                  :input-value="selectedCabs.includes(cab)"
                  :disabled="!cabIsReady(account, cab).value"
                  :ripple="false"
                  @change="selectCab(cab)"
                />
              </template>

              <template #[`item.adAccountName`]="{item: cab}">
                {{ cab.name }}
              </template>

              <!-- СТАТУС КАБИНЕТА -->
              <template #[`item.adAccountStatus`]="{item: cab}">
                <cabs-status
                  :cab="cab"
                  :show-notes="false"
                />
              </template>

              <!-- ПРОГРЕСС-БАР -->
              <template #[`item.jobStatus`]="{item: cab}">
                <div v-if="cabIsReady(account, cab).value">
                  <v-progress-linear
                    v-if="!responses[cab.id]"
                    :indeterminate="loadingCabs.includes(cab.id)"
                    :value="0"
                    style="min-width: 50px;"
                  />
              
                  <span v-else-if="responses && responses[cab.id] && responses[cab.id]['success']">
                    <v-icon color="green">
                      check_box
                    </v-icon>
                  </span>

                  <span v-else-if="responses && responses[cab.id] && !responses[cab.id]['success']">
                    <v-icon color="red">
                      cancel
                    </v-icon>
                    <span
                      style="font-size: 12px; cursor: pointer; color: #2196F3; text-decoration: underline;"
                      @click="openDetails(responses[cab.id]['response'])"
                    >{{ $t('common.learnMore') }}</span>
                  </span>
                </div>
                
                <span
                  v-else
                  style="color: #9E9E9E;"
                >
                  {{ cabIsReady(account, cab).text }}
                </span>
              </template>
            </v-data-table>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import clipboard        from '@/mixins/clipboard';
import {mapGetters}     from 'vuex';

import CabsStatus       from '../cabs/CabsMainTableStatus.vue';

export default {
  name: 'AccountsDialogAdAccountsManagement',

  components: {
    CabsStatus
  },

  mixins: [clipboard],

  data() {
    return {
      detailsDialog: false,
      forDetails: [],
      loading: false,
      cabs: [],
      selectedCabs: [],
      responses: {},
      loadingCabs: []
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/accounts',
      dialogs: 'accounts_v2/dialogs'
    }),

    colsAccounts() {
      let cols = [];

      cols.push({text: this.$t('common.account'), value: 'account', width: 150});
      cols.push({text: this.$t('common.adAccount'), value: 'adAccount'});

      return cols;
    },

    colsCabs() {
      let cols = [];

      cols.push({text: this.$t('common.adAccount'), value: 'adAccountName', width: 150});
      cols.push({text: this.$t('common.adAccountStatus'), value: 'adAccountStatus', width: 150, sortable: false});
      cols.push({text: this.$t('common.jobStatus'), value: 'jobStatus', sortable: false});

      return cols;
    },
  },

  created () {
    this.loadCabs();
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('accounts_v2/closeDialog', 'adAccountsManagement');
    },

    cabsFromAccount(account) {
      const cabs = [];

      this.cabs.forEach(i => {
        if (parseInt(i.account.id, 10) === parseInt(account.id, 10)) cabs.push(i);
      });

      return cabs;
    },

    async loadCabs() {
      const data = {
        users_ids: -1,
        accounts_ids: this.accounts.selected && this.accounts.selected.map(account => account.id),
      };

      const response = await this.api.post('/cabs', data);
      if (response.status === 200 && response.data) {
        this.cabs = response.data.data;
      }
    },

    async removeCabs() {
      try {
        this.loading = true;
        this.responses = {},
        this.loadingCabs = [];

        for (const cab of this.selectedCabs) {
          this.loadingCabs.push(cab.id);
        }
      
        for (const cab of this.selectedCabs) {          
          const response = await this.api.post(`/accounts/quit_from_personal_cab/?account_id=${cab.account.id}&cab_id=${cab.cab_id}`);
          this.loadingCabs = this.loadingCabs.filter(i => i !== cab.id);

          if (response.status === 200 && response.data.result) {
            this.$set(this.responses, cab.id, { 
              success: true
            });
            this.selectedCabs = this.selectedCabs.filter(i => parseInt(i.id, 10) !== parseInt(cab.id, 10));
            this.cabs = this.cabs.filter(i => parseInt(i.id, 10) !== parseInt(cab.id, 10));
          } else {
            this.$set(this.responses, cab.id, {
              success: false,
              response: response
            });
          }
        }
      } catch (err) {
        // Do nothing
      } finally {
        this.loading = false;
        this.loadingCabs = [];
      }
    },

    cabIsReady(account, cab) {

      if (account && account.status !== 'ACTIVE' && account.status !== 'ACTIVITY_BLOCK') {
        return {
          text: this.$t('common.errorMessages.accountIsNotActive'), 
          value: false
        };
      }
      if (account && this.$_.isEmpty(account.cookies)) {
        return {
          text: this.$t('common.errorMessages.accountHasNoCookies'), 
          value: false
        };
      }
      if (account && !account.lsd) {
        return {
          text: this.$t('common.errorMessages.accountHasNoLsdToken'),
          value: false
        };
      }
      if (account && !account.fbdtsg) {
        return {
          text: this.$t('common.errorMessages.accountHasNoFbdtsgToken'),
          value: false
        };
      }
      if (cab.business_id !== null) {
        return {
          text: this.$t('common.errorMessages.notPersonalAdAccount'),
          value: false
        };
      }

      return {value: true};
    },

    openDetails(response) {
      this.forDetails = response;
      this.detailsDialog = true;
    },

    selectCab(cab) {
      if (this.selectedCabs.includes(cab)) {
        this.selectedCabs = this.selectedCabs.filter(i => parseInt(i.id, 10) !== parseInt(cab.id, 10));
      } else {
        this.selectedCabs.push(cab);
      }
    }
  }
};
</script>

<style>
.accounts-dialog-ad-accounts-management-cabs-table tbody tr:last-child td {
  border-bottom: none !important;
}
.accounts-dialog-ad-accounts-management-cabs-table tbody tr td:first-child {
  width: 40px;
}
.accounts-dialog-ad-accounts-management-cabs-table tbody tr td:nth-child(2) {
  width: 150px;
}
.accounts-dialog-ad-accounts-management-cabs-table tbody tr td:nth-child(3) {
  width: 150px;
}
</style>