<template>
  <div>
    <!-- МОДАЛЬНОЕ ОКНО -->
    <v-dialog
      v-if="showModal"
      :value="dialogs.createBm"
      persistent
      fullscreen
      @keydown.esc="close"
    >
      <v-card :loading="loading.createBm">
        <v-toolbar
          color="primary"
        >
          <!-- КНОПКА ЗАКРЫТЬ -->
          <v-btn
            icon
            @click="close"
          >
            <v-icon>fas fa-times</v-icon>
          </v-btn>

          <!-- ЗАГОЛОВОК -->
          <v-toolbar-title>{{ $t('dialogs.accounts.createBm.title') }}</v-toolbar-title>
          <v-spacer />

          <!-- КНОПКА СОЗДАТЬ -->
          <v-toolbar-items>
            <v-btn
              text
              @click="createBms"
            >
              {{ $t('common.create') }}
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>

        <!-- ОСНОВНОЙ КОНТЕЙНЕР -->
        <v-card-text>
          <v-container>
            <v-row
              align="center"
            >
              <v-col
                cols="8"
              >
                <div style="float: left;">
                  <v-select
                    dense
                    :items="['Business names']"
                    value="Business names"
                    style="width: 300px;"
                    persistent-hint
                    :hint="$t('dialogs.accounts.createBm.takeFromDictionary')"
                  />
                </div>
                <div
                  style="float: left;"
                >
                  <v-btn
                    text
                    color="primary"
                    @click="takeNamesFromDictionary"
                  >
                    {{ $t('common.take') }}
                  </v-btn>
                </div>
                <div style="float: left;">
                  <v-btn
                    text
                    color="primary"
                    @click="randomEmails"
                  >
                    {{ $t('dialogs.accounts.createBm.randomEmails') }}
                  </v-btn>
                </div>
              </v-col>
            </v-row>
            <v-row
              align="center"
            >
              <v-col cols="12">
                <v-data-table
                  :headers="cols"
                  fixed-header
                  :items="accountsToProcess"
                >
                  <template #item.account="{item: account}">
                    {{ account.name }}
                  </template>

                  <template #item.bmName="{item: account}">
                    <v-text-field
                      v-model="accountsToProcess[accountsToProcess.indexOf(account)].newBmName"
                      :label="$t('dialogs.accounts.createBm.bmName')"
                      single-line
                      dense
                      hide-details
                      class="my-1 mr-4"
                      :style="$vuetify.breakpoint.sm || $vuetify.breakpoint.xs ? 'margin-right: 2px !important;' : ''"
                      solo
                      clearable
                      @input="clearInput($event, accountsToProcess.indexOf(account), 'newBmName')"
                    />
                  </template>

                  <template #item.email="{item: account}">
                    <v-text-field
                      v-model="accountsToProcess[accountsToProcess.indexOf(account)].newBmEmail"
                      :label="$t('common.email')"
                      single-line
                      dense
                      hide-details
                      class="my-1 mr-4"
                      :style="$vuetify.breakpoint.sm || $vuetify.breakpoint.xs ? 'margin-right: 2px !important;' : ''"
                      solo
                      clearable
                      @input="clearInput($event, accountsToProcess.indexOf(account), 'newBmEmail')"
                    />
                  </template>

                  <template #item.status="{item: account}">
                    <v-chip
                      v-if="account.bmCreationStatus === 'waiting'"
                      color="orange"
                      outlined
                      x-small
                      label
                      style="font-weight: bold;"
                    >
                      {{ $t('common.waiting') }}
                    </v-chip>

                    <v-chip
                      v-if="account.bmCreationStatus === 'requesting'"
                      color="orange"
                      outlined
                      x-small
                      label
                      style="font-weight: bold;"
                    >
                      {{ $t('common.requesting') }}
                    </v-chip>

                    <v-chip
                      v-if="account.bmCreationStatus === 'success'"
                      color="success"
                      outlined
                      x-small
                      label
                      style="font-weight: bold;"
                    >
                      {{ $t('common.success') }}
                    </v-chip>

                    <v-chip
                      v-if="account.bmCreationStatus === 'error'"
                      color="error"
                      outlined
                      x-small
                      label
                      style="font-weight: bold;"
                    >
                      {{ $t('common.error') }}
                    </v-chip>
                  </template>

                  <template #item.result="{item: account}">
                    <div
                      style="font-family: monospace; font-size: 11px; color: #FF5252; font-weight: bold;"
                      class="my-3"
                    >
                      <div v-if="account.bmCreationResponse.response !== undefined && account.bmCreationResponse.response.error !== undefined && account.bmCreationResponse.response.error.message !== undefined">
                        {{ account.bmCreationResponse.response.error.message }}
                      </div>
                      <div v-if="account.bmCreationResponse.response !== undefined && account.bmCreationResponse.response.error !== undefined && account.bmCreationResponse.response.error.error_user_title !== undefined">
                        {{ account.bmCreationResponse.response.error.error_user_title }}
                      </div>
                      <div v-if="account.bmCreationResponse.response !== undefined && account.bmCreationResponse.response.error !== undefined && account.bmCreationResponse.response.error.error_user_msg !== undefined">
                        {{ account.bmCreationResponse.response.error.error_user_msg }}
                      </div>
                      <div v-if="account.bmCreationResponse.message !== undefined">
                        {{ account.bmCreationResponse.message }}
                      </div>
                    </div>
                  </template>
                </v-data-table>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import randomEmail    from 'random-email';
import {mapGetters}   from 'vuex';
    
export default {
  name: 'AccountsDialogCreateBm',

  data() {
    return {
      cols: [],
      validation: {
        state: false,
      },
      newAccount: {},
      accountsToProcess: [],
      multiple: {
        name: null,
        categoryId: null
      },
      showLoading: false,
      showModal: true,
      done: 0,
      initialAccountsToProcess: []
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/ACCOUNTS',
      dialogs: 'accounts_v2/dialogs',
      loading: 'accounts_v2/loading',
    })
  },

  created() {
    this.cols.push({
      text: this.$t('common.account'),
      value: 'account',
      width: 200
    });
    this.cols.push({
      text: this.$t('dialogs.accounts.createBm.bmName'),
      value: 'bmName',
      width: 200,
    });
    this.cols.push({
      text: this.$t('common.email') + ' (' + this.$t('common.optional').toLowerCase() + ')',
      value: 'email',
      width: 200,
    });
    this.cols.push({
      text: this.$t('common.status'),
      value: 'status',
      width: 150,
    });
    this.cols.push({
      text: this.$t('common.result'),
      value: 'result',
      width: 150,
    });

    // this.accountsToProcess = this.accountsToProcess.concat(this.accounts.selected);
    this.accountsToProcess = this.$_.cloneDeep(this.accounts.selected);
  
    this.accountsToProcess = this.accountsToProcess.map(account => {
      account.bmCreationStatus = 'waiting';
      account.bmCreationResponse = {};
      account.newBmName = null;
      account.newBmEmail = null;
      return account;
    });

    this.initialAccountsToProcess = this.$_.cloneDeep(this.accountsToProcess);
  },

  methods: {
    clearInput(event, index, property) {
      if (event === '') {
        this.accountsToProcess[index][property] = null;
      }
    },

    close() {
      if (!this.$_.isEqual(this.accountsToProcess, this.initialAccountsToProcess)) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('accounts_v2/closeDialog', 'createBm');
    },

    createBms() {
      if (!this.accountsToProcess) {
        this.$store.dispatch('accounts_v2/closeDialog', 'createBm');
        return;
      }
      
      this.accountsToProcess.forEach((account, index) => {
        account.bmCreationStatus = 'requesting';
        
        this.$set(this.accountsToProcess, index, account);
        
        const data = {
          id: account.id,
          name: account.newBmName,
          email: account.newBmEmail,
        };
        this.api.post('/accounts/create_bm', data).then(response => {
          account.bmCreationStatus = (typeof response.data.response === 'undefined' || typeof response.data.response.error !== 'undefined' || response.data.success === false)
            ? 'error' : 'success';
          account.bmCreationResponse = response.data;
          this.$set(this.accountsToProcess, index, account);
        });
      });
    },

    async takeNamesFromDictionary() {
      const response = await this.api.post('/generate_names', {count: this.accountsToProcess.length});
      this.accountsToProcess = this.accountsToProcess.map((account, index) => {
        account.newBmName = response.data.data[index];
        return account;
      });
    },

    randomEmails() {
      this.accountsToProcess = this.accountsToProcess.map((account) => {
        account.newBmEmail = randomEmail({domain: 'gmail.com'});
        return account;
      });
    }
  }
};
</script>