<template>
  <v-dialog
    :value="dialogs.multipleAssignTags"
    max-width="600px"
    :persistent="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? false : true"
    :scrollable="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? true : false"
    @click:outside="close"
    @keydown.esc="close"
  >
    <v-card :loading="loading.multipleAssignTags">
      <!-- ЗАГОЛОВОК -->
      <v-card-title>
        <span class="headline">
          {{ $t('dialogs.accounts.multipleAssignTags.title') }}
        </span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col
              cols="12"
              class="pa-0"
            >
              <v-btn-toggle
                v-model="tabs"
                color="primary"
                borderless
                multiple
                mandatory
                style="text-align: center;"
              >
                <v-btn
                  value="accounts"  
                  style="font-size: 12px; height: 24px;"
                >
                  <span>{{ $t('upload.wizard.static.tags.accounts') }}</span>
                </v-btn>

                <v-btn
                  value="cabs"  
                  style="font-size: 12px; height: 24px;"
                >
                  <span>{{ $t('upload.wizard.static.tags.cabs') }}</span>
                </v-btn>

                <v-btn
                  value="campaigns"  
                  style="font-size: 12px; height: 24px;"
                >
                  <span>{{ $t('upload.wizard.static.tags.campaigns') }}</span>
                </v-btn>

                <v-btn
                  value="adsets"  
                  style="font-size: 12px; height: 24px;"
                >
                  <span>{{ $t('upload.wizard.static.tags.adsets') }}</span>
                </v-btn>

                <v-btn
                  value="ads"  
                  style="font-size: 12px; height: 24px;"
                >
                  <span>{{ $t('upload.wizard.static.tags.ads') }}</span>
                </v-btn>
              </v-btn-toggle>
            </v-col>
            <v-col
              cols="12"
            >
              <!-- ТЕГИ -->
              <v-combobox
                v-model="selectedTags"
                :label="$t('common.tags')"
                :items="computedTags"
                multiple
                chips
                deletable-chips
                clearable
                hide-details
              >
                <template #item="{ item }">
                  {{ item }}
                  <v-spacer />
                  <v-list-item-action @click.stop>
                    <v-btn
                      small
                      icon
                      @click="excludeTag(item)"
                    >
                      <v-icon :size="16">
                        fas fa-times
                      </v-icon>
                    </v-btn>
                  </v-list-item-action>
                </template>
              </v-combobox>

              <!-- ВЫБОР РЕЖИМА -->
              <div>
                <v-radio-group
                  v-model="mode"
                  row
                  dense
                  style="margin-top: 0px;"
                  hide-details
                  class="mt-3"
                >
                  <v-radio
                    :label="$t('dialogs.accounts.multipleAssignTags.addTags')"
                    :value="1"
                  />
                  <v-radio
                    :label="$t('dialogs.accounts.multipleAssignTags.removeTags')"
                    :value="-1"
                  />
                </v-radio-group>
              </div>
            </v-col>
            <v-col :cols="12">
              <v-alert
                text
                color="primary"
                style="font-size: 12px; padding: 8px; line-height: 14px;"
              >
                <p v-html="$t('dialogs.accounts.multipleAssignTags.magicTags.description')" />
                <ul>
                  <li class="my-1">
                    <v-chip
                      class="mr-1"
                      color="red"
                      outlined
                      x-small
                      label
                      style="cursor: pointer;"
                      @click="addTag($t('dialogs.accounts.multipleAssignTags.magicTags.noUpdatesTag'))"
                    >
                      {{ $t('dialogs.accounts.multipleAssignTags.magicTags.noUpdatesTag') }}
                    </v-chip> - {{ $t('dialogs.accounts.multipleAssignTags.magicTags.noUpdatesTagDesc') }}
                  </li>
                  <li class="my-1">
                    <v-chip
                      class="mr-1"
                      color="red"
                      outlined
                      x-small
                      label
                      style="cursor: pointer;"
                      @click="addTag($t('dialogs.accounts.multipleAssignTags.magicTags.noCommentsTag'))"
                    >
                      {{ $t('dialogs.accounts.multipleAssignTags.magicTags.noCommentsTag') }}
                    </v-chip> - {{ $t('dialogs.accounts.multipleAssignTags.magicTags.noCommentsTagDesc') }}
                  </li>
                </ul>
              </v-alert>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="close"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          color="blue darken-1"
          text
          :disabled="!selectedTags.length"
          @click="saveTags"
        >
          {{ $t('common.save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'AccountsMultipleAssignTagsDialog',

  data() {
    return {
      tabs: ['accounts'],
      mode: 1,
      selectedTags: [],
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      currentLevelTags: 'tags/currentLevelTags',
      accounts: 'accounts_v2/selected',
      loading: 'accounts_v2/loading'
    }),

    computedTags() {
      if (this.mode === -1) {
        let assignedTags = [];

        this.accounts.forEach(account => {
          assignedTags = assignedTags.concat(account.tags);
        });

        return assignedTags;
      }
      return this.currentLevelTags.all;
    }
  },

  created() {
    // this.$store.dispatch('tags/loadTags');
    this.$store.dispatch('tags/loadItems', {
      only_from: 'accounts',
      exclude_tags: 0
    });
  },

  methods: {
    close() {
      if (this.selectedTags.length !== 0) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('accounts_v2/closeDialog', 'multipleAssignTags');
    },

    addTag(tag) {
      if (this.selectedTags.indexOf(tag.trim()) === -1) {
        this.selectedTags.push(tag.trim());
      }
    },

    async saveTags() {
      if (this.tabs.includes('accounts')) {
        const data = {
          ids: this.accounts.map(account => account.id),
          tags: this.selectedTags,
          mode: this.mode,
        };
        const success = await this.$store.dispatch('accounts_v2/saveMultipleTags', data);
        if (success) {
          this.$store.dispatch('tags/loadTags');
          this.$store.dispatch('accounts_v2/closeDialog', 'multipleAssignTags');
        }
      } else {
        this.addTagsToSelectedTabs();

        this.$store.dispatch('main/alert', {
          color: 'success',
          message: this.$t('common.saved')
        });

        this.$store.dispatch('accounts_v2/closeDialog', 'multipleAssignTags');
      }

      if (this.tabs.includes('accounts') && this.tabs.length > 1) {
        this.addTagsToSelectedTabs();
      }
    },

    async addTagsToSelectedTabs() {      
      for (const tab of this.tabs) {
        switch (tab) {
        case 'cabs':
          await this.addTagsToCabs();
          break;
        case 'campaigns':
          await this.addTagsToCampaigns();
          break;
        case 'adsets':
          await this.addTagsToAdsets();
          break;
        case 'ads':
          await this.addTagsToAds();
          break;
        }
      }
    },

    async addTagsToCabs() {
      const response = await this.api.post('/cabs', { 
        users_ids: -1,
        accounts_ids: this.accounts.map(account => account.id) 
      })
        .catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });

      if (response.status === 200 && response.data.data) {
        const data = {
          ids: response.data.data.map(i => i.id),
          tags: this.selectedTags,
          mode: this.mode,
        };

        await this.api.post('/cabs/update_multiple_tags', data).catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });
      }
    },

    async addTagsToCampaigns() {
      const response = await this.api.post('/campaigns', { 
        users_ids: -1,
        accounts_ids: this.accounts.map(account => account.id),
        cabs_ids: -1
      })
        .catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });

      if (response.status === 200 && response.data.data) {
        const data = {
          ids: response.data.data.map(i => i.id),
          tags: this.selectedTags,
          mode: this.mode,
        };

        await this.api.post('/campaigns/update_multiple_tags', data).catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });
      }
    },

    async addTagsToAdsets() {
      const response = await this.api.post('/adsets', { 
        users_ids: -1,
        accounts_ids: this.accounts.map(account => account.id),
        cabs_ids: -1,
        campaigns_ids: -1
      })
        .catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });

      if (response.status === 200 && response.data.data) {
        const data = {
          ids: response.data.data.map(i => i.id),
          tags: this.selectedTags,
          mode: this.mode,
        };

        await this.api.post('/adsets/update_multiple_tags', data).catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });
      }
    },

    async addTagsToAds() {
      const response = await this.api.post('/ads',{ 
        users_ids: -1,
        accounts_ids: this.accounts.map(account => account.id),
        cabs_ids: -1,
        campaigns_ids: -1,
        adsets_ids: -1,
      })
        .catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });

      if (response.status === 200 && response.data.data) {
        const data = {
          ids: response.data.data.map(i => i.id),
          tags: this.selectedTags,
          mode: this.mode,
        };

        await this.api.post('/ads/update_multiple_tags', data).catch((e) => {
          this.$store.dispatch('main/apiError', e, {
            root: true
          });
        });
      }
    },

    async excludeTag(tagToExclude) {
      await this.api.post('/profile/exclude_tag', {tag: tagToExclude});
      await this.$store.dispatch('tags/loadItems', {
        only_from: 'accounts',
        exclude_tags: 0
      });
    }

  }
};
</script>
