<template>
  <v-row>
    <v-col
      cols="12"
      md="8"
    >
      <v-alert
        color="primary"
        text 
        v-html="$t('adsmanager.accounts.add.loginPassword.formats')"
      />
      <v-textarea
        :value="importDialog.tab3.dataRaw"
        rows="10"
        dense
        outlined
        auto-grow
        hide-details
        @input="setImportDialogOptions('dataRaw', $event)"
      />
      <v-alert
        v-if="alertUseragent"
        class="mt-4"
        color="red"
        text
        hide-details
        v-html="$t('adsmanager.accounts.add.alertUseragent')"
      />
    </v-col>
    <v-col
      cols="12"
      md="4"
    >
      <v-simple-table
        height="400"
        fixed-header
        class="add-accounts-data-input-table"
      >
        <template v-slot:default>
          <thead>
            <tr>
              <th>{{ $t('common.id') }}</th>
              <th>{{ $t('common.nameProxy') }}</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(proxy, index) in proxy.all"
              :key="`new-proxy-${index}`"
            >
              <td>{{ proxy.id }}</td>
              <td>{{ proxy.name }}</td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-col>
  </v-row>
</template>
<script>
import useragents      from '@/constants/useragents';
import {mapGetters}    from 'vuex';

export default {
  name: 'AccountsDialogLoginPasswordDataInput',

  data() {
    return {
      data: [],
      useragents,
      alertUseragent: false
    };
  },

  computed: {
    ...mapGetters({
      proxy: 'proxy/proxy',
      importDialog: 'accounts_v2/importDialog'
    })
  },

  watch: {
    'importDialog.tab3.dataRaw'() {
      this.parse();
    },

    data: {
      deep: true,
      handler() {
        this.setImportDialogOptions('newAccounts', this.data);
      }
    }
  },

  methods: {
    setImportDialogOptions(option, value) {
      let data = {
        category: 'tab3',
        option: option,
        value: value
      };
      this.$store.dispatch('accounts_v2/setImportDialogOptions', data);
    },
    parse() {
      this.alertUseragent = false;
      const lines = this.importDialog.tab3.dataRaw.split('\n');     
      this.data = [];
      this.$store.dispatch('accounts_v2/setTab3NewProxies', []);
      let newProxies = this.importDialog.tab3.newProxies;
      let leastLoaded = {
        free: true,
        id: null,
        ip: null,
        port: null,
        login: null,
        password: null,
        name: null,
        accounts_count: null,
        type: null,
      };
      newProxies.push(leastLoaded);
      
      if (lines.length === 0) {
        return;
      }
      lines.forEach(line => {
        if (!line.trim()) return;

        let data = {
          name: null,
          login: null,
          password: null,
          user_agent: null,
          proxy: {
            id: null,
            ip: null,
            port: null,
            login: null,
            password: null,
            name: null,
            accounts_count: 0,
            type: null
          },
          newProxy: null,
          tags: null
        };

        if (line.split('::').length > 1) {
          const nameLine = line.split('::')[0];
          if (nameLine)  data.name = nameLine.trim();

          const nameLogin = line.split('::')[1].trim();
          if (nameLogin)  data.login = nameLogin.trim();
          
          const namePassword = line.split('::')[2].trim();
          if (namePassword)  data.password = namePassword.trim();

          const nameUserAgent = line.split('::')[3].trim();
          if (nameUserAgent)  data.user_agent = nameUserAgent.trim();
          
          if (line.split('::')[3].trim().toLowerCase().indexOf('android') > -1) {
            this.alertUseragent = true;
          }

          if (data.user_agent.toString().length === 0) {
            data.user_agent = useragents[parseInt(Math.random() * useragents.length)];
          }

          if (line.split('::').length > 4) {
            let proxy = line.split('::')[4].trim();
            if (proxy.indexOf(':') === -1) {
              let existingProxy = this.proxy.all.find(p => p.id === parseInt(proxy));
              data.proxy = {...existingProxy};
              data.newProxy = null;
            } else {
              data.newProxy = line.split('::')[4].trim();
              let proxyType = null;
              if (proxy.indexOf('/') === -1) {
                proxyType = 'http';
              } else {
                if (proxy.split('://').length > 1) {
                  proxyType = proxy.split('://')[0].toLowerCase();
                  if (proxyType === 'https') proxyType = 'http';
                  if (proxyType === 'socks') proxyType = 'socks5';
                  proxy = proxy.split('://')[1];
                }

              }
              if (proxy.split(':').length > 1) {
                if (proxy.split(':').length === 2) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: null,
                    password: null,
                    name: null,
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
                if (proxy.split(':').length === 3) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: null,
                    password: null,
                    name: proxy.split(':')[2].trim(),
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
                if (proxy.split(':').length === 4) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: proxy.split(':')[2].trim(),
                    password: proxy.split(':')[3].trim(),
                    name: null,
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
                if (proxy.split(':').length === 5) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: proxy.split(':')[2].trim(),
                    password: proxy.split(':')[3].trim(),
                    name: proxy.split(':')[4].trim(),
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
              }
            }
          }
        }
        if (data.name && data.login && data.password) {
          this.data.push(data);
        }
        if (data.proxy.id === null && data.proxy.ip !== null && data.proxy.port !== null) {
          newProxies.push(data.proxy);
          this.$store.dispatch('accounts_v2/setTab3NewProxies', newProxies);
        }
      });
    }
  }
};
</script>
<style>
.add-accounts-data-input-table td {
  height: 32px;
}
</style>