<template>
  <div>
    <v-dialog
      :value="dialogs.approveDeletion"
      scrollable
      width="600"
      @click:outside="close"
      @keydown.esc="close"
    >
      <v-card>
        <v-card-title>
          {{ $t('adsmanager.accounts.deletingAccounts') }}
        </v-card-title>
        <v-card-text>
          <v-data-table
            :items="accounts.selected"
            hide-default-header
            hide-default-footer
            :headers="cols"
            :items-per-page="-1"
            style="max-height: 500px;"
          >
            <!-- ИНФО -->
            <template #[`item.info`]="{item: account}">
              <!-- ID -->
              <span style="color: grey;">
                [{{ account.id }}]
              </span>

              <!-- ЛОГИ -->
              <v-icon
                :size="12"
                style="cursor: pointer; margin-right: 4px;"
                :title="$t('common.fbApiLog')"
                @click.stop.prevent="loadLog(account)"
              >
                fas fa-book
              </v-icon>

              <!-- НАЗВАНИЕ АККАУНТА -->
              <span
                style="cursor: pointer; color: #2196F3; text-decoration: underline; display: inline-block; max-width: 150px;"
                @click.stop.prevent="editAccount(account)"
              >
                {{ account.name }}
              </span>

              <!-- Нет FB ID! Что это? -->
              <div
                v-if="!account.fb_id"
                style="width: 90%; margin-top: 4px;"
              >
                <v-alert
                  text
                  color="error"
                  style="padding: 0 5px; font-size: 10px; line-height: 17px;"
                >
                  {{ $t('adsmanager.accounts.noFacebookId') }}
                  <a
                    style="font-size: 10px;"
                    @click.stop.prevent="$store.dispatch('accounts_v2/openDialog', 'noFacebookIdInfo')"
                  >
                    {{ $t('common.whatsThat') }}
                  </a>
                </v-alert>
              </div>
            </template>

            <!-- СТАТУС -->
            <template #[`item.status`]="{item: account}">
              <v-chip
                v-if="colors[account.status] !== undefined"
                label
                :color="colors[account.status]"
                x-small
                outlined
                style="font-weight: bold;"
              >
                <span v-if="account.status === 'TOKEN_ERROR'">
                  ERROR
                </span>
                <span v-else>
                  {{ account.status }}
                </span>
              </v-chip>
              <span v-if="colors[account.status] === undefined">
                {{ account.status }}
              </span>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-btn
            color="blue darken-1"
            text
            @click="deleteAccounts"
          >
            {{ $t('common.delete') }}
          </v-btn>
          
          <v-spacer />

          <v-btn
            color="blue darken-1"
            text
            @click="close"
          >
            {{ $t('common.cancel') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {mapGetters}     from 'vuex';

import colors           from '../../../constants/accounts/status-colors';

export default {
  name: 'AccountsDialogApproveDeletion',

  data() {
    return {
      colors
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/ACCOUNTS',
      dialogs: 'accounts_v2/dialogs',
    }), 

    cols() {
      let cols = [];

      cols.push({value: 'info'});
      cols.push({value: 'status'});

      return cols;
    }
  },

  methods: {
    close () {
      this.$store.dispatch('accounts_v2/closeDialog', 'approveDeletion');
    },

    editAccount(account) {
      this.$store.dispatch('accounts_v2/initAccountForEdit', account);
    },

    async loadLog(account) {
      this.$store.dispatch('fbApiLog/clearLog');
      this.$store.dispatch('fbApiLog/openDialog', 'logSidebar');
      this.$store.dispatch('fbApiLog/loadLog', [{object: 'account_id', id: account.id}]);
    },

    deleteAccounts () {
      this.$store.dispatch(
        'accounts_v2/multipleDeleteAccounts',
        this.accounts.selected.map(account => account.id)
      );
      this.close();
    }
  }
};
</script>