<script>
import CurrencySelector     from '@/components/adsmanager/AdsManagerCurrencySelectorV2';
import Refresh              from '@/components/adsmanager/AdsManagerRefresh';
import { mapGetters }       from 'vuex';

import accountsStatuses     from '../../../constants/accounts/accounts-statuses';
import TopbarCols           from '../AdsManagerTopbarCols';
import FiltersDate          from '../filters/AdsManagerFiltersDateV2';

import FiltersBar           from './AccountsFiltersBar';
import TopbarActions        from './AccountsTopbarActions';

export default {
  name: 'AccountsTopbar',

  components: {
    CurrencySelector,
    Refresh,
    FiltersBar,
    FiltersDate,
    TopbarActions,
    TopbarCols,
  },

  data() {
    return {
      nameSearchText: '',
      accountsStatuses,
      showFilters: false,
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/ACCOUNTS',
      filters: 'accounts_v2/FILTERS',
      tags: 'tags/tags'
    }),

    activeFiltersCount() {
      let count = 0;

      if (!this.filters) return 0;

      if (
        typeof this.filters.statuses !== 'undefined' &&
        this.filters.statuses &&
        this.filters.statuses.length > 0
      ) count++;
      if (
        typeof this.filters.tags !== 'undefined' &&
        this.filters.tags &&
        this.filters.tags.length > 0
      ) count++;
      if (
        typeof this.filters.showArchived !== 'undefined' &&
        this.filters.showArchived
      ) count++;
      if (
        typeof this.filters.withSpend !== 'undefined' &&
        this.filters.withSpend
      ) count++;

      return count;
    }
  },

  methods: {
    filterName(name) {
      localStorage.setItem('adsmanager-accounts-filters', JSON.stringify(this.filters));
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      this.nameSearchText = name;
      setTimeout(async () => {
        if (name === this.nameSearchText) {
          await this.$store.dispatch('accounts_v2/setFiltersName', name);
          // this.$store.dispatch('accounts_v2/loadStat');
        }
      }, 500);
    }
  }
};
</script>

