<template>
  <td>
    <span
      v-if="show"
    >
      <span v-if="col === 'action_results-cr'">
        {{ 
          itIsTotalRow ? 
            numeral(stat['cr']).format('0.00') :
            numeral(item['cr']).format('0.00')
        }}%
      </span>
      <span v-else>
        {{ 
          itIsTotalRow ? 
            numeral(stat['actions'][col.slice(0, col.length - 3)]['cr']).format('0.00') :
            numeral(item['actions'][col.slice(0, col.length - 3)]['cr']).format('0.00')
        }}%
      </span>
    </span>
    <span v-else>
      0%
    </span>
  </td>
</template>

<script>
import numeral        from 'numeral';

export default {
  name: 'AdsManagerComplexStatCellCr',

  props: {
    item: {
      type: Object,
      default: () => ({})
    },
    col: {
      type: String,
      default: '',
    },
    itIsTotalRow: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    return {
      numeral
    };
  },

  computed: {
    stat() {
      return this.$store.getters[`${this.$store.state.adsmanager.activeTab.component}_v2/stat`];
    },

    show() {
      if (!this.stat) return false;
      
      if (this.col === 'action_results-cr') {
        if(this.itIsTotalRow) {
          return (
            typeof this.stat !== 'undefined' &&
            typeof this.stat['cr'] !== 'undefined'
          );
        }

        return (
          typeof this.item !== 'undefined' &&
          typeof this.item['cr'] !== 'undefined'
        ); 
      }

      if(this.itIsTotalRow) {
        return (
          typeof this.stat !== 'undefined' &&
          typeof this.stat['actions'] !== 'undefined' &&
          typeof this.stat['actions'][this.col.slice(0, this.col.length - 3)] !== 'undefined'
        );
      }

      return (
        typeof this.stat[this.item.id] !== 'undefined' &&
        typeof this.stat[this.item.id]['actions'] !== 'undefined' &&
        typeof this.stat[this.item.id]['actions'][this.col.slice(0, this.col.length - 3)] !== 'undefined' 
      );
    }
  },
};
</script>