<template>
  <div>
    <v-dialog
      :value="dialogs.acceptRules"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        class="pr-4"
      >
        <v-card-text>
          <div style="display: flex; justify-content: space-between; align-items: center; min-height: 40px; color: white;">
            <div>
              {{ $t('dialogs.accounts.acceptRules.loadingText') }}
            </div>
            <div>
              {{ acceptRulesLoading.done }} / {{ acceptRulesLoading.accounts.length }}
            </div>
          </div>
          
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          />

          <v-btn
            icon
            style="position: absolute; right: 2px; top: 2px;"
            small
            @click="$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'stopRequest', value: true});"
          >
            <v-icon
              :size="14"
              color="white"
            >
              fas fa-times-circle
            </v-icon>
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios                     from 'axios';
import {mapGetters}              from 'vuex';

export default {
  name: 'AccountsDialogAcceptRules',

  data() {
    return {
      done: 0,
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/ACCOUNTS',
      dialogs: 'accounts_v2/dialogs',
      acceptRulesLoading: 'accounts_v2/acceptRulesLoading'
    })
  },

  watch: {
    'acceptRulesLoading.stopRequest': {
      handler() {
        if (this.acceptRulesLoading.stopRequest) {
          for (let request of this.acceptRulesLoading.source) {
            request.cancel();
          }
          this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'showLoading', value: false});
        }
      }
    }
  },

  created() {
    if (this.acceptRulesLoading.requestStatus) return;
    if (!confirm(this.$t('common.confirmPlease'))) return this.$store.dispatch('accounts_v2/closeDialog', 'acceptRules');
    this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'requestStatus', value: true});
    this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'stopRequest', value: false});

    if (this.accounts.selected && this.accounts.selected.length > 0) {
      this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'accounts', value: this.accounts.selected});
      this.acceptRulesLoading.accounts.forEach((account, index) => {
        const CancelToken = axios.CancelToken;
        let sourceData = {
          dialog: 'acceptRulesLoading',
          index: index,
          source: CancelToken.source(),
        };
        this.$store.dispatch('accounts_v2/setLoadingSource', sourceData);
        this.api(`/accounts/accept_rules/${account.id}`, {
          cancelToken: this.acceptRulesLoading.source[index].token
        }).catch(() => true)
          .then((response) => {
            console.log(JSON.stringify(response.data));
          })
          .finally(() => {
            this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'done', value: this.acceptRulesLoading.done + 1});
            if (this.acceptRulesLoading.done === this.acceptRulesLoading.accounts.length) {
              this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'done', value: 0});
              this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'acceptRulesLoading', option: 'requestStatus', value: false});
              this.$store.dispatch('accounts_v2/closeDialog', 'acceptRules');
              this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
            }
          });
      });
    }
  }
};
</script>
<style scoped>
  .v-dialog__content {
    align-items: flex-end !important;
    justify-content: flex-end !important;
  }
</style>