<template>
  <v-stepper
    v-model="step"
    class="dialog-stepper"
  >
    <!-- ДОБАВИТЬ МНОГО - ХЭДЕР -->
    <v-stepper-header>
      <v-stepper-step
        :step="1"
        editable
      >
        {{ $t('adsmanager.accounts.add.bulk.dataInput') }}
      </v-stepper-step>
      <v-stepper-step
        :step="2"
        editable
        @click="openStep2"
      >
        {{ $t('adsmanager.accounts.add.bulk.dataCheck') }}
      </v-stepper-step>
    </v-stepper-header>
    <!-- ДОБАВИТЬ МНОГО - КОНТЕНТ -->
    <v-stepper-items>
      <!-- ВВОД ДАННЫХ -->
      <v-stepper-content :step="1">
        <data-input />
      </v-stepper-content>
      <!-- ПРОВЕРКА ДАННЫХ -->
      <v-stepper-content :step="2">
        <data-check />
      </v-stepper-content>
    </v-stepper-items>
    <v-row>
      <v-col style="text-align: center;">
        <v-btn
          v-if="step === 1"
          color="primary"
          raised
          @click="step++, openStep2()"
        >
          {{ $t('common.continue') }}
        </v-btn>
      </v-col>
    </v-row>
  </v-stepper>
</template>
<script>  
import useragents       from '@/constants/useragents';
import {mapGetters}     from 'vuex';

import DataCheck        from './AccountsDialogBulkAddDataCheck';
import DataInput        from './AccountsDialogBulkAddDataInput';

export default {
  name: 'AccountsDialogBulkAdd',

  components: {
    DataInput,
    DataCheck
  },

  data() {
    return {
      step: 1,
      useragents
    };
  },

  computed: {
    ...mapGetters({
      importDialog: 'accounts_v2/importDialog'
    })
  },

  methods: {
    openStep2() {
      for (let accountIndex in this.importDialog.tab2.newAccounts) {
        if (this.importDialog.tab2.newAccounts[accountIndex].user_agent === '' 
          || this.importDialog.tab2.newAccounts[accountIndex].user_agent === null
          || this.importDialog.tab2.newAccounts[accountIndex].user_agent === 'User agent') {
          let max = this.useragents.length;
          let min = 1;
          let randomUseragent = Math.floor(Math.random() * (max - min + 1) + min);
          let data = {
            index: accountIndex,
            option: 'user_agent',
            value: this.useragents[randomUseragent - 1]
          };
          this.$store.dispatch('accounts_v2/setTab2NewAccountsOptions', data);
        }
      }
    }
  }
};
</script>