<template>
  <v-row>
    <v-col
      cols="12"
      md="8"
    >
      <v-alert
        color="primary"
        text 
        v-html="$t('adsmanager.accounts.add.bulk.formats')"
      />
      <v-textarea
        v-model="importDialog.tab2.dataRaw"
        rows="10"
        dense
        outlined
        auto-grow
        hide-details
      />
    </v-col>
    <v-col
      cols="12"
      md="4"
    >
      <v-simple-table
        height="400"
        fixed-header
        class="add-accounts-data-input-table"
      >
        <template v-slot:default>
          <thead>
            <tr>
              <th>{{ $t('common.id') }}</th>
              <th>{{ $t('common.nameProxy') }}</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(proxy, index) in proxy.all"
              :key="`new-proxy-${index}`"
            >
              <td>{{ proxy.id }}</td>
              <td>{{ proxy.name }}</td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-col>
  </v-row>
</template>
<script>
import {mapGetters}    from 'vuex';

export default {
  name: 'AccountsDialogBulkAddDataInput',

  data() {
    return {
      data: []
    };
  },

  computed: {
    ...mapGetters({
      proxy: 'proxy/proxy',
      importDialog: 'accounts_v2/importDialog'
    })
  },

  watch: {
    'importDialog.tab2.dataRaw'() {
      this.parse();
    },

    data: {
      deep: true,
      handler() {
        this.importDialog.tab2.newAccounts = this.data;
      }
    }
  },

  methods: {
    parse() {
      const lines = this.importDialog.tab2.dataRaw.split('\n');     
      this.data = [];
      this.$store.dispatch('accounts_v2/setTab2NewProxies', []);
      let newProxies = this.importDialog.tab2.newProxies;
      let leastLoaded = {
        free: true,
        id: null,
        ip: null,
        port: null,
        login: null,
        password: null,
        name: null,
        accounts_count: null,
        type: null,
      };
      if (this.proxy && this.proxy.all && Array.isArray(this.proxy.all) && this.proxy.all.length > 0) {
        newProxies.push(leastLoaded);
      }
      
      if (lines.length === 0) return;
      
      for(const line of lines) {
        if (!line.trim()) return;

        let partialLine = line.split('::[');
        let splittedLine = [];
        
        if (partialLine.length > 1) {
          splittedLine = partialLine[0].split('::');
          
          const lineWithCookies = partialLine[1].split(']::');
          splittedLine.push(`[${lineWithCookies[0]}]`);

          if (lineWithCookies.length > 1) {
            splittedLine.push(lineWithCookies[1]);
          }

        } else {
          splittedLine = line.split('::');
        } 

        let data = {
          name: null,
          access_token: null,
          user_agent: null,
          proxy: {
            id: null,
            ip: null,
            port: null,
            login: null,
            password: null,
            name: null,
            accounts_count: 0,
            type: null
          },
          newProxy: null,
          tags: null,
          cookies: null,
          password: null,
        };
        if (splittedLine.length > 1) {

          data.name = splittedLine[0].trim();
          data.access_token = splittedLine[1] ? splittedLine[1].trim() : '';
          data.user_agent = splittedLine[2] ? splittedLine[2].trim() : '';

          if (splittedLine.length > 3) {
            let proxy = splittedLine[3].trim();
            if (proxy.indexOf(':') === -1) {
              let existingProxy = this.proxy.all.find(p => p.id === parseInt(proxy));
              data.proxy = {...existingProxy};
              data.newProxy = null;
            } else {
              data.newProxy = splittedLine[3].trim();
              let proxyType = null;
              if (proxy.indexOf('/') === -1) {
                proxyType = 'http';
              } else {
                if (proxy.split('://').length > 1) {
                  proxyType = proxy.split('://')[0].toLowerCase();
                  if (proxyType === 'https') proxyType = 'http';
                  if (proxyType === 'socks') proxyType = 'socks5';
                  proxy = proxy.split('://')[1];
                }

              }
              if (proxy.split(':').length > 1) {
                if (proxy.split(':').length === 2) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: null,
                    password: null,
                    name: null,
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
                if (proxy.split(':').length === 3) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: null,
                    password: null,
                    name: proxy.split(':')[2].trim(),
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
                if (proxy.split(':').length === 4) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: proxy.split(':')[2].trim(),
                    password: proxy.split(':')[3].trim(),
                    name: null,
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
                if (proxy.split(':').length === 5) {
                  data.proxy = {
                    id: null,
                    ip: proxy.split(':')[0].trim(),
                    port: proxy.split(':')[1].trim(),
                    login: proxy.split(':')[2].trim(),
                    password: proxy.split(':')[3].trim(),
                    name: proxy.split(':')[4].trim(),
                    accounts_count: 0,
                    type: proxyType,
                  };
                }
              }
            }
          }
          data.cookies = splittedLine[4] ? splittedLine[4].trim() : '';
          data.password = splittedLine[5] ? splittedLine[5].trim() : '';
        }

        if (data.name && data.access_token) {
          this.data.push(data);
        }
        if (data.proxy.id === null && data.proxy.ip !== null && data.proxy.port !== null) {
          newProxies.push(data.proxy);
          this.$store.dispatch('accounts_v2/setTab2NewProxies', newProxies);
        }
      }
    }
  }
};
</script>
<style>
.add-accounts-data-input-table td {
  height: 32px;
}
</style>