<template>
  <div id="viewAdsmanagerAccounts">
    <!-- ДИАЛОГИ -->
    <single-add-dialog v-if="dialogs.add" />
    <edit-dialog v-if="dialogs.edit" />
    <multiple-share-dialog v-if="dialogs.multipleShare" />
    <multiple-assign-tags-dialog v-if="dialogs.multipleAssignTags" />
    <assign-tags-dialog v-if="dialogs.assignTags" />
    <share-dialog v-if="dialogs.share" />
    <permissions-sidebar v-if="dialogs.permissions" />
    <dialog-accept-rules v-if="dialogs.acceptRules" />
    <dialog-create-bm v-if="dialogs.createBm" />
    <dialog-create-fan-page v-if="dialogs.createFanPage" />
    <dialog-init-personal-cab v-if="dialogs.initPersonalCab" />
    <dialog-move-accounts-to-another-user v-if="dialogs.moveAccountsToAnotherUser" />
    <dialog-no-facebook-id-info v-if="dialogs.noFacebookIdInfo" />
    <dialog-unarchive v-if="dialogs.unarchive" />
    <dialog-main-info v-if="dialogs.mainInfo" />
    <dialog-change-proxy v-if="dialogs.changeProxy" />
    <dialog-approve-deletion v-if="dialogs.approveDeletion" />
    <dialog-share-fan-page v-if="dialogs.shareFanPage" />
    <dialog-additional-tokens-info v-if="dialogs.additionalTokensInfo" />
    <dialog-ad-accounts-management v-if="dialogs.adAccountsManagement" />
    <dialog-page-appeal v-if="dialogs.pageAppeal" />
    <dialog-turn-off-page-notifications v-if="dialogs.turnOffPageNotifications" />

    <!-- TOPBAR -->
    <topbar />

    <!-- ТАБЛИЦА -->
    <main-table />
  </div>
</template>

<script>
import DialogAcceptRules                                                   from '@/components/adsmanager/accounts-new/AccountsDialogAcceptRules';
import DialogAdAccountsManagement                                          from '@/components/adsmanager/accounts-new/AccountsDialogAdAccountsManagement';
import DialogAdditionalTokensInfo                                          from '@/components/adsmanager/accounts-new/AccountsDialogAdditionalTokensInfo';
import DialogApproveDeletion                                               from '@/components/adsmanager/accounts-new/AccountsDialogApproveDeletion';
import DialogChangeProxy                                                   from '@/components/adsmanager/accounts-new/AccountsDialogChangeProxy';
import DialogCreateBm                                                      from '@/components/adsmanager/accounts-new/AccountsDialogCreateBm';
import DialogCreateFanPage                                                 from '@/components/adsmanager/accounts-new/AccountsDialogCreateFanPage';
import DialogInitPersonalCab                                               from '@/components/adsmanager/accounts-new/AccountsDialogInitPersonalCab';
import DialogMainInfo                                                      from '@/components/adsmanager/accounts-new/AccountsDialogMainInfo';
import DialogMoveAccountsToAnotherUser                                     from '@/components/adsmanager/accounts-new/AccountsDialogMoveAccountsToAnotherUser';
import DialogNoFacebookIdInfo                                              from '@/components/adsmanager/accounts-new/AccountsDialogNoFacebookIdInfo';
import DialogPageAppeal                                                    from '@/components/adsmanager/accounts-new/AccountsDialogPageAppeal';
import DialogShareFanPage                                                  from '@/components/adsmanager/accounts-new/AccountsDialogShareFanPage';
import DialogTurnOffPageNotifications                                      from '@/components/adsmanager/accounts-new/AccountsDialogTurnOffPageNotifications';
import DialogUnarchive                                                     from '@/components/adsmanager/accounts-new/AccountsDialogUnarchive';
import {mapGetters}                                                        from 'vuex';

import AssignTagsDialog                                                    from '../../components/adsmanager/accounts-new/AccountsAssignTagsDialog';
import EditDialog                                                          from '../../components/adsmanager/accounts-new/AccountsEditDialog';
import MainTable                                                           from '../../components/adsmanager/accounts-new/AccountsMainTable';
import MultipleAssignTagsDialog                                            from '../../components/adsmanager/accounts-new/AccountsMultipleAssignTagsDialog';
import MultipleShareDialog                                                 from '../../components/adsmanager/accounts-new/AccountsMultipleShareDialog';
import PermissionsSidebar                                                  from '../../components/adsmanager/accounts-new/AccountsPermissionsSidebar';
import ShareDialog                                                         from '../../components/adsmanager/accounts-new/AccountsShareDialog';
import SingleAddDialog                                                     from '../../components/adsmanager/accounts-new/AccountsSingleAddDialog';
import Topbar                                                              from '../../components/adsmanager/accounts-new/AccountsTopbar';

export default {
  name: 'Accounts',

  components: {
    DialogAcceptRules,
    DialogChangeProxy,
    DialogCreateBm,
    DialogCreateFanPage,
    DialogInitPersonalCab,
    DialogMainInfo,
    DialogMoveAccountsToAnotherUser,
    DialogNoFacebookIdInfo,
    DialogUnarchive,
    DialogApproveDeletion,
    Topbar,
    MainTable,
    AssignTagsDialog,
    ShareDialog,
    SingleAddDialog,
    EditDialog,
    MultipleShareDialog,
    MultipleAssignTagsDialog,
    PermissionsSidebar,
    DialogShareFanPage,
    DialogAdditionalTokensInfo,
    DialogAdAccountsManagement,
    DialogPageAppeal,
    DialogTurnOffPageNotifications
  },

  data() {
    return {
      updateStatInterval: 0,
      scrollSetted: false,
      table: null,
      scrollPosition: 0,
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      globalFilters: 'adsmanager/filters',
      accounts: 'accounts_v2/accounts'
    })
  },

  watch: {
    globalFilters: {
      deep: true,
      async handler() {
        await this.$store.dispatch('accounts_v2/filterAccounts');
        // this.$store.dispatch('accounts/loadStat');
      }
    },

    // 'accounts.selected': {
    //   deep: true,
    //   handler(newVal, oldVal) {
    //     if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
    //       this.$store.dispatch('bms/clear');
    //       this.$store.dispatch('cabs/clear');
    //       this.$store.dispatch('campaigns/clear');
    //       this.$store.dispatch('adsets/clear');
    //       this.$store.dispatch('ads/clear');
    //     }
    //   }
    // },

    'accounts.filtered': {
      deep: true,
      handler() {
        this.table = document.querySelector('.stat-table-accounts .v-data-table__wrapper');
        if (this.table && this.table.scrollHeight > this.table.clientHeight && !this.scrollSetted) {
          if (localStorage.getItem('adsmanager-accounts-table-scroll-position')) {
            this.table.scrollTop = parseInt(localStorage.getItem('adsmanager-accounts-table-scroll-position'));
          }
          this.table.addEventListener('scroll', this.tableScrollHandler);
          this.scrollSetted = true;
        }

        // if (Array.isArray(newVal) && newVal.length > 0 && Array.isArray(oldVal) && !this.$_.isEqual(newVal, oldVal)) {
        //   this.$store.dispatch('accounts/loadStat');
        // }
      }
    }
  },

  created() {
    this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
    this.$store.dispatch('users/loadUsers');
    this.updateStatInterval = setInterval( () => {
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      this.$store.dispatch('users/loadUsers');
    }, 60000 );
  },

  mounted() {
    this.$store.dispatch('accounts_v2/filterAccounts');
  },

  beforeDestroy() {
    // this.table.removeEventListener('scroll', this.tableScrollHandler);
    clearInterval(this.updateStatInterval);
  },

  methods: {
    tableScrollHandler(e) {
      if (!e || !e.target) return; 
      
      this.scrollPosition = e.target.scrollTop;

      setTimeout(() => {
        if (e && e.target && e.target.scrollTop === this.scrollPosition) {
          localStorage.setItem('adsmanager-accounts-table-scroll-position', e.target.scrollTop);
        }
      }, 1000);
    }
  }
};
</script>
