<template>
  <v-navigation-drawer
    :value="show"
    temporary
    right
    absolute
    style="z-index: 9999; width: 400px;"
    @transitionend="$emit('close')"
  >
    <div style="padding: 16px">
      <div class="mb-3">
        <span>
          <v-btn
            color="grey"
            fab
            x-small
            outlined
            class="sidebar-collapse-button"
            @click="$emit('close')"
          >
            <v-icon>keyboard_arrow_right</v-icon>
          </v-btn>
        </span>
        <span class="title">
          {{ $t('common.filters') }}
        </span>
      </div>

      <!-- ФИЛЬТР ПО СТАТУСУ -->
      <v-row>
        <v-col
          cols="12"
          class="py-0 my-2"
        >
          <v-combobox
            :label="$t('filters.status')"
            :items="accountsStatuses"
            :value="filters.statuses"
            multiple
            chips
            deletable-chips
            clearable
            dense
            small-chips
            single-line
            solo
            prepend-inner-icon="fas fa-code"
            hide-details
            @change="filterStatus"
          />
        </v-col>

        <!-- ФИЛЬТР ПО ТЕГАМ -->
        <v-col
          cols="12"
          class="py-0 mt-2"
        >
          <v-combobox
            :label="$t('common.tags')"
            :items="currentLevelTags.all"
            :value="filters.tags"
            multiple
            chips
            deletable-chips
            clearable
            dense
            small-chips
            solo
            prepend-inner-icon="fas fa-tags"
            hide-details
            @change="filterTags"
          />
        </v-col>

        <!-- ФИЛЬТР ПО АРХИВИРОВАННЫМ -->
        <v-col
          cols="12"
          class="py-0 mt-2"
        >
          <h5>
            {{ $t('adsmanager.accounts.filters.archived') }}
          </h5>
          <v-checkbox
            v-model="filters.showArchived"
            :label="$t('adsmanager.accounts.filters.showArchivedAccounts')"
            @change="$store.dispatch('accounts_v2/setSpecificFilter', {filter: 'showArchived', data: $event ? true : false});"
          />
        </v-col>

        <!-- ФИЛЬТР ПО РАСХОДУ -->
        <v-col
          cols="12"
          class="py-0 mt-2"
        >
          <h5>
            {{ $t('common.spend') }}
          </h5>
          <v-checkbox
            v-model="filters.withSpend"
            :label="$t('common.withSpend')"
            @change="$store.dispatch('accounts_v2/setSpecificFilter', {filter: 'withSpend', data: $event ? true : false});"
          />
        </v-col>
      </v-row>

      <!-- ОЧИСТИТЬ ФИЛЬТРЫ -->
      <v-col
        v-if="$vuetify.breakpoint.xs || $vuetify.breakpoint.sm"
        cols="12"
        class="py-0 mt-2"
      >
        <v-btn
          v-if="activeFiltersCount > 0"
          text
          block
          color="primary"
          small
          class="mr-1"
          @click="$store.dispatch('accounts_v2/clearFilters')"
        >
          <v-icon
            class="mr-1"
            :size="18"
          >
            close
          </v-icon>
          {{ $t('common.clear') }} {{ $t('common.filters') }}
        </v-btn>
      </v-col>

      <!-- КНОПКИ ДЕЙСТВИЙ - СТОЛБЦЫ -->
      <v-col
        v-if="$vuetify.breakpoint.xs || $vuetify.breakpoint.sm"
        cols="12"
        class="py-0 mt-2"
      >
        <topbar-cols />
      </v-col>

      <!-- ФИЛЬТР ПО ТЕГАМ -->
    </div>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters }             from 'vuex';

import accountsStatuses           from '../../../constants/accounts/accounts-statuses';
import TopbarCols                 from '../AdsManagerTopbarCols';

export default {
  name: 'AccountsFiltersBar',

  components: {
    TopbarCols,

  },

  props: {
    show: {
      type: Boolean,
      default: false,
    },

    activeFiltersCount: {
      type: Number,
      default: 0,
    }
  },

  data() {
    return {
      loadCurrentLevelTagsInterval: 0,
      accountsStatuses
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/ACCOUNTS',
      filters: 'accounts_v2/FILTERS',
      currentLevelTags: 'tags/currentLevelTags',
    })
  },

  watch: {
    'filters.withSpend': {
      handler () {
        this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      },
      deep: false,
    },

    'filters.showArchived': {
      handler () {
        this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      },
      deep: false,
    }
  },

  created() {
    this.filters.newVersion = true;
    this.$store.dispatch('tags/loadItems', {
      only_from: 'accounts',
      exclude_tags: 0
    });
    
    this.loadCurrentLevelTagsInterval = setInterval(() => {
      this.$store.dispatch('tags/loadItems', {
        only_from: 'accounts',
        exclude_tags: 0
      });
    }, 60000);
  },

  beforeDestroy() {
    this.filters.newVersion = false;
    clearInterval(this.loadCurrentLevelTagsInterval);
  },

  methods: {
    async filterStatus(statuses) {
      if (statuses) this.$store.dispatch('accounts_v2/clearSelected');
      await this.$store.dispatch('accounts_v2/setFiltersStatuses', statuses.map(status => {
        if (typeof status.value !== 'undefined') return status.value;
        return status;
      }));

      localStorage.setItem('adsmanager-accounts-filters', JSON.stringify(this.filters));
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
    },

    async filterTags(tags) {
      if (tags && tags.length > 0) {
        this.$store.dispatch('accounts_v2/clearSelected');
      }
      const data = {
        filter: 'tags',
        data: tags
      };
      await this.$store.dispatch('accounts_v2/setSpecificFilter', data);
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
    }
  }
};
</script>
