<template>
  <td>
    <span
      v-if="show"
    >
      <span v-if="col === 'action_results-cpa'">
        {{ 
          itIsTotalRow ? 
            numeral(stat['cpa']).format('0,0.[00]').split(',').join(' ') :
            numeral(item['cpa']).format('0,0.[00]').split(',').join(' ')
        }}
      </span>
      <span v-else>
        {{ 
          itIsTotalRow ? 
            numeral(stat['actions'][col.slice(0, col.length - 4)]['cpa']).format('0,0.[00]').split(',').join(' ') :
            numeral(item['actions'][col.slice(0, col.length - 4)]['cpa']).format('0,0.[00]').split(',').join(' ')
        }}
      </span>
    </span>
    <span v-else>
      0
    </span>
    <span> {{ currency }}</span>
  </td>
</template>

<script>
import numeral            from 'numeral';
import {mapGetters}       from 'vuex';

export default {
  name: 'AdsManagerComplexStatCellCpa',

  props: {
    item: {
      type: Object,
      default: () => ({})
    },
    col: {
      type: String,
      default: '',
    },
    itIsTotalRow: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    return {
      numeral
    };
  },

  computed: {
    ...mapGetters({
      currency: 'adsmanager/currency'
    }),

    stat() {
      return this.$store.getters[`${this.$store.state.adsmanager.activeTab.component}_v2/stat`];
    },

    show() {
      if (!this.stat) return false;
      
      if (this.col === 'action_results-cpa') {
        if(this.itIsTotalRow) {
          return (
            typeof this.stat !== 'undefined' &&
                      typeof this.stat['cpa'] !== 'undefined'
          );
        }

        return (
          typeof this.item !== 'undefined' &&
                    typeof this.item['cpa'] !== 'undefined'
        ); 
      }

      if(this.itIsTotalRow) {
        return (
          typeof this.stat !== 'undefined' &&
                    typeof this.stat['actions'] !== 'undefined' &&
                    typeof this.stat['actions'][this.col.slice(0, this.col.length - 4)] !== 'undefined'
        );
      }

      return (
        typeof this.item !== 'undefined' &&
                    typeof this.item['actions'] !== 'undefined' &&
                    typeof this.item['actions'][this.col.slice(0, this.col.length - 4)] !== 'undefined'
      );
    }
  },
};
</script>