<template>
  <div>
    <div>
      <v-dialog
        v-model="detailsDialog"
        width="700"
        scrollable
      >
        <v-card>
          <v-card-title class="headline">
            {{ $t('common.learnMore') }}
          </v-card-title>

          <v-card-text>
            <div v-if="typeof forDetails.images !== 'undefined'">
              <code v-if="!forDetails.result && forDetails.out">
                {{ forDetails.out }}
              </code>
              <img
                v-for="img in forDetails.images"
                :key="img"
                class="my-1"
                style="width: 100%; height: auto;"
                :src="baseUrl + img"
              >
            </div>
            <div v-if="importDialog.tab3.mode === 'api'">
              <code>{{ JSON.stringify(forDetails.response) }}</code>
            </div>
          </v-card-text>

          <v-divider />

          <v-card-actions>
            <v-spacer />
            <v-btn
              color="primary"
              text
              @click="detailsDialog = false;"
            >
              {{ $t('common.close') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <div>
      <v-data-table
        :headers="cols"
        fixed-header
        :items="importDialog.tab3.newAccounts"
      >
        <template #item.name="{item: accounts}">
          <!-- НАЗВАНИЕ -->
          <v-text-field
            :value="accounts.name"
            :label="$t('common.name')"
            class="my-2 mr-2"
            outlined
            dense
            hide-details
            @input="setTab3NewAccountsOptions(importDialog.tab3.newAccounts.indexOf(accounts), 'name', $event)"
          />
        </template>
        <template #item.login="{item: accounts}">
          <!-- ЛОГИН -->
          <v-text-field
            :value="accounts.login"
            :label="$t('common.login')"
            class="my-2 mr-2"
            outlined
            dense
            hide-details
            @input="setTab3NewAccountsOptions(importDialog.tab3.newAccounts.indexOf(accounts), 'login', $event)"
          />
        </template>
        <template #item.password="{item: accounts}">
          <!-- ПАРОЛЬ -->
          <v-text-field
            :value="accounts.password"
            :label="$t('common.password')"
            class="my-2 mr-2"
            outlined
            dense
            hide-details
            @input="setTab3NewAccountsOptions(importDialog.tab3.newAccounts.indexOf(accounts), 'password', $event)"
          />
        </template>
        <template #item.useragent="{item: accounts}">
          <!-- USERAGENT -->
          <v-text-field
            :value="accounts.user_agent"
            :label="$t('common.useragent')"
            class="my-2 mr-2"
            outlined
            dense
            hide-details
            @input="setTab3NewAccountsOptions(importDialog.tab3.newAccounts.indexOf(accounts), 'user_agent', $event)"
          >
            <!-- КНОПКА РАНДОМИЗАЦИИ -->
            <template 
              #prepend-inner
            >
              <v-btn
                x-small
                icon
                @click="randomizeUseragent(importDialog.tab3.newAccounts.indexOf(accounts))"
              >
                <v-icon style="margin-top: 3px;">
                  autorenew
                </v-icon>
              </v-btn>
            </template>

            <!-- ШАБЛОН КНОПКИ СКОПИРОВАТЬ -->
            <template #append>
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    icon
                    x-small
                    style="margin-top: 2px;"
                    @click="copyToAnotherAccounts('user_agent', importDialog.tab3.newAccounts.indexOf(accounts))"
                    v-on="on"
                  >
                    <v-icon>clear_all</v-icon>
                  </v-btn>
                </template>
                <span>{{ $t('adsmanager.accounts.add.copyToAnotherAccounts') }}</span>
              </v-tooltip>
            </template>
          </v-text-field>
        </template>
        <template
          #item.proxy="{item: accounts}"
          style="width: 300px;"
        >
          <!-- ПРОКСИ -->
          <v-autocomplete
            :value="accounts.proxy"
            :items="importDialog.tab3.newProxies.concat(proxy.all)"
            :item-value="item => item"
            :label="accounts.proxy.id ? $t('common.existingProxy') : $t('common.proxyNew')"
            :filter="searchExistingProxy"
            class="my-2"
            :class="{'mr-2': !$vuetify.breakpoint.xs}"
            outlined
            dense
            hide-details
            :placeholder="$t('common.proxyNo').toLowerCase()"
            @input="setTab3NewAccountsOptions(importDialog.tab3.newAccounts.indexOf(accounts), 'proxy', $event)"
          >
            <template v-slot:item="{item}">
              {{
                typeof item.name !== 'undefined' && item.name !== null ?
                  '[' + item.accounts_count +'] ' + item.name :
                  item.login === null && item.free ?
                    $t('adsmanager.accounts.add.leastLoaded') :
                    item.login === null ?
                      '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}` :
                      '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}:${item.login}:${item.password}`
              }}
            </template>
            <template v-slot:selection="{item}">
              {{
                typeof item.name !== 'undefined' && item.name !== null ?
                  '[' + item.accounts_count +'] ' + item.name :
                  item.login === null && item.free ?
                    $t('adsmanager.accounts.add.leastLoaded') :
                    item.login === null ?
                      '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}` :
                      '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}:${item.login}:${item.password}`
              }}
            </template>
            <!-- ШАБЛОН КНОПКИ СКОПИРОВАТЬ -->
            <template #append-outer>
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    icon
                    x-small
                    @click="copyToAnotherAccounts('proxy', importDialog.tab3.newAccounts.indexOf(accounts))"
                    v-on="on"
                  >
                    <v-icon>clear_all</v-icon>
                  </v-btn>
                </template>
                <span>{{ $t('adsmanager.accounts.add.copyToAnotherAccounts') }}</span>
              </v-tooltip>
            </template>
          </v-autocomplete>
        </template>
        <template
          #item.tags="{item: accounts}"
          class="pr-3"
        >
          <!-- ТЕГИ -->
          <v-combobox
            :value="accounts.tags"
            :label="$t('common.tags')"
            :items="tags.all"
            class="my-2"
            :class="{'mr-2': !$vuetify.breakpoint.xs}"
            single-line
            :dense="!accounts.tags || accounts.tags && accounts.tags.length === 0 ? true : false"
            small-chips
            multiple
            chips
            deletable-chips
            clearable
            outlined
            hide-details
            @input="setTab3NewAccountsOptions(importDialog.tab3.newAccounts.indexOf(accounts), 'tags', $event)"
          >
            <!-- ШАБЛОН КНОПКИ СКОПИРОВАТЬ -->
            <template #append-outer>
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-btn
                    icon
                    x-small
                    style="margin-top: 2px;"
                    @click="copyToAnotherAccounts('tags', importDialog.tab3.newAccounts.indexOf(accounts))"
                    v-on="on"
                  >
                    <v-icon>clear_all</v-icon>
                  </v-btn>
                </template>
                <span>{{ $t('adsmanager.accounts.add.copyToAnotherAccounts') }}</span>
              </v-tooltip>
            </template>
          </v-combobox>
        </template>

        <!-- ПРОГРЕСС-БАР -->
        <template #item.status="{item: accounts}">
          <v-progress-linear
            v-if="typeof processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)] === 'undefined' || 
              !processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)] || 
              processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)].toString().length === 0"
            :indeterminate="typeof processData.loading[importDialog.tab3.newAccounts.indexOf(accounts)] !== 'undefined' ? 
              processData.loading[importDialog.tab3.newAccounts.indexOf(accounts)] : false"
            :value="0"
          />
          <span
            v-if="typeof processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)] !== 'undefined' && 
              typeof processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)].result !== 'undefined' && 
              processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)].result"
          >
            <v-icon color="green">check_box</v-icon>
            <a
              href="#"
              style="font-size: 12px;"
              @click="openDetails(processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)])"
            >
              {{ $t('common.learnMore') }}
            </a>
          </span>
          <span
            v-if="typeof processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)] !== 'undefined' && 
              typeof processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)].result !== 'undefined' && 
              !processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)].result"
          >
            <v-icon
              color="red"
              class="mr-1"
            >cancel</v-icon>
            <a
              href="#"
              style="font-size: 12px;"
              @click="openDetails(processData.responses[importDialog.tab3.newAccounts.indexOf(accounts)])"
            >
              {{ $t('common.learnMore') }}
            </a>
          </span>
        </template>
      </v-data-table>
    </div>
  </div>
</template>
<script>
import useragents      from '@/constants/useragents';
import {mapGetters}    from 'vuex';

export default {
  name: 'AccountsDialogLoginPasswordDataCheck',

  props: {
    processData: {
      type: Object,
      default: () => ({
        responses: {},
        loading: {},
      })
    }
  },

  data() {
    return {
      data: [],
      currentAccount: null,
      detailsDialog: false,
      forDetails: {},
      useragents
    };
  },

  computed: {
    ...mapGetters({
      proxy: 'proxy/proxy',
      tags: 'tags/tags',
      importDialog: 'accounts_v2/importDialog'
    }),

    cols() {
      let cols = [];

      cols.push({text: this.$t('common.name'), value: 'name', width: 200,});
      cols.push({text: this.$t('common.login'), value: 'login', width: 200, sortable: false});
      cols.push({text: this.$t('common.password'), value: 'password', width: 200, sortable: false});
      cols.push({text: this.$t('common.useragent'), value: 'useragent', width: 200, sortable: false});
      cols.push({text: this.$t('common.proxy'), value: 'proxy', width: 200, sortable: false});
      cols.push({text: this.$t('common.tags'), value: 'tags', width: 200, sortable: false});
      cols.push({text: this.$t('common.status'), value: 'status', sortable: false});

      return cols;
    }
  },

  methods: {
    setTab3NewAccountsOptions(index, option, value) {
      let data = {
        index: index,
        option: option,
        value: value
      };
      this.$store.dispatch('accounts_v2/setTab3NewAccountsOptions', data);
    },

    searchExistingProxy(item, queryText) {
      const q = queryText.toLowerCase();
      if (typeof item.name !== undefined && item.name && item.name.toString().toLowerCase().search(q) !== -1) return true;
      if (item.ip && item.ip.toString().toLowerCase().search(q) !== -1) return true;
      if (item.port && item.port.toString().toLowerCase().search(q) !== -1) return true;
      if (item.login && item.login.toString().toLowerCase().search(q) !== -1) return true;
      if (item.password && item.password.toString().toLowerCase().search(q) !== -1) return true;

      return false;
    },

    copyToAnotherAccounts(what, index) {
      for (const i in this.importDialog.tab3.newAccounts) {
        this.setTab3NewAccountsOptions(i, what, this.importDialog.tab3.newAccounts[index][what]);
      }
    },

    openDetails(response) {
      this.forDetails = response;
      this.detailsDialog = true;
    },

    randomizeUseragent(index) {
      let max = this.useragents.length;
      let min = 1;
      let randomUseragent = Math.floor(Math.random() * (max - min + 1) + min);
      return this.setTab3NewAccountsOptions(index, 'user_agent', this.useragents[randomUseragent - 1]);
    }
  }
};
</script>
<style>
.data-check-table .v-data-table__wrapper {
  overflow-x: hidden;

}
</style>