<template>
  <div>
    <!-- МОДАЛЬНОЕ ОКНО -->
    <v-dialog
      :value="dialogs.moveAccountsToAnotherUser"
      max-width="600px"
      :persistent="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? false : true"
      :scrollable="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? true : false"
      @click:outside="close"
      @keydown.esc="close"
    >
      <v-card :loading="loading.moveAccounts">
        <v-card-title>
          <div style="display: flex; justify-content: space-between; width: 100%;">
            <div>
              <span class="headline">
                {{ $t('adsmanager.accounts.moveAccountsToAnotherUser') }}
              </span>
            </div>
          </div>
        </v-card-title>
        <v-card-text style="max-height: 700px;">
          <v-container>
            <v-row>
              <!-- ПОИСК ПОЛЬЗОВАТЕЛЯ -->
              <v-col
                cols="12"
              >
                <v-combobox
                  :item-text="user => user.display_name || user.login"
                  :item-value="user => user.display_name || user.login"
                  :item-disabled="user => ([2,4].indexOf(profile.role_id) > -1 && user.id == profile.id)"
                  :items="allUsers"
                  :label="$t('common.user')"
                  clearable
                  @change="selectedUser=$event"
                />
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="blue darken-1"
            text
            @click="close"
          >
            {{ $t('common.close') }}
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="moveAccounts()"
          >
            {{ $t('common.move') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'AccountsDialogMoveAccountsToAnotherUser',

  data() {
    return {
      selectedUser: null,
      allUsers: [],
    };
  },
  computed: {
    ...mapGetters({
      profile: 'main/profile',
      accounts: 'accounts_v2/ACCOUNTS',
      loading: 'accounts_v2/loading',
      dialogs: 'accounts_v2/dialogs'
    }),
 
  },
  created() {
    this.loadUsers();
  },
  methods: {
    close() {
      if (this.selectedUser) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('accounts_v2/closeDialog', 'moveAccountsToAnotherUser');
    },

    async moveAccounts() {
      if (!this.selectedUser && !this.selectedUser.id) {
        return;
      }
      if (!confirm(this.$t('adsmanager.accounts.confirmMoving') + ' ' + (this.selectedUser.display_name || this.selectedUser.login))) {
        return;
      }
      const data = {
        ids: this.accounts.selected.map(account => account.id),
        new_user_id: this.selectedUser.id
      };
      await this.$store.dispatch('accounts_v2/moveAccountsToAnotherUser', data);
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      this.$store.dispatch('accounts_v2/closeDialog', 'moveAccountsToAnotherUser');
    },

    async loadUsers() {
      const response = await this.api('/users/all');
      if (typeof response.data.data !== 'undefined') {
        this.allUsers = response.data.data;
      }
    }
  }
};
</script>
