<template>
  <div>
    <v-data-table
      :headers="cols"
      fixed-header
      :items="importDialog.tab2.newAccounts"
    >
      <template #[`item.name`]="{item: accounts}">
        <!-- НАЗВАНИЕ -->
        <v-text-field
          v-model="accounts.name"
          :label="$t('common.name')"
          class="my-2 mr-2"
          outlined
          dense
          hide-details
          style="min-width: 250px;"
        />
      </template>
      <template #[`item.access_token`]="{item: accounts}">
        <!-- ТОКЕН -->
        <v-text-field
          v-model="accounts.access_token"
          :label="$t('common.access_token')"
          class="my-2 mr-2"
          outlined
          dense
          hide-details
        />
      </template>
      <template #[`item.useragent`]="{item: accounts}">
        <!-- USERAGENT -->
        <v-text-field
          v-model="accounts.user_agent"
          :label="$t('common.useragent')"
          class="my-2 mr-2"
          outlined
          dense
          hide-details
        >
          <!-- КНОПКА РАНДОМИЗАЦИИ -->
          <template 
            #prepend-inner
          >
            <v-btn
              x-small
              icon
              @click="randomizeUseragent(importDialog.tab2.newAccounts.indexOf(accounts))"
            >
              <v-icon style="margin-top: 3px;">
                autorenew
              </v-icon>
            </v-btn>
          </template>

          <!-- ШАБЛОН КНОПКИ СКОПИРОВАТЬ -->
          <template #append>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn
                  icon
                  x-small
                  style="margin-top: 2px;"
                  @click="copyToAnotherAccounts('user_agent', importDialog.tab2.newAccounts.indexOf(accounts))"
                  v-on="on"
                >
                  <v-icon>clear_all</v-icon>
                </v-btn>
              </template>
              <span>{{ $t('adsmanager.accounts.add.copyToAnotherAccounts') }}</span>
            </v-tooltip>
          </template>
        </v-text-field>
      </template>
      <template #[`item.proxy`]="{item: accounts}">
        <!-- ПРОКСИ -->
        <!-- <div v-if="accounts.proxy.id"> -->
        <v-autocomplete
          v-model="accounts.proxy"
          :items="importDialog.tab2.newProxies.concat(proxy.all)"
          :item-value="item => item"
          :label="accounts.proxy.id ? $t('common.existingProxy') : $t('common.proxyNew')"
          :filter="searchExistingProxy"
          class="my-2"
          :class="{'mr-2': !$vuetify.breakpoint.xs}"
          outlined
          dense
          hide-details
          :placeholder="$t('common.proxyNo').toLowerCase()"
        >
          <template v-slot:item="{item}">
            {{ 
              typeof item.name !== 'undefined' && item.name !== null ?
                '[' + item.accounts_count +'] ' + item.name :
                item.login === null && item.free ?
                  $t('adsmanager.accounts.add.leastLoaded') :
                  item.login === null ?
                    '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}` :
                    '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}:${item.login}:${item.password}`
            }}
          </template> 
          <template v-slot:selection="{item}">
            {{ 
              typeof item.name !== 'undefined' && item.name !== null ?
                '[' + item.accounts_count +'] ' + item.name :
                item.login === null && item.free ?
                  $t('adsmanager.accounts.add.leastLoaded') :
                  item.login === null ?
                    '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}` :
                    '[' + item.accounts_count +'] ' + `${item.ip}:${item.port}:${item.login}:${item.password}`
            }}
          </template> 
          <!-- ШАБЛОН КНОПКИ СКОПИРОВАТЬ -->
          <template #append-outer>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn
                  icon
                  x-small
                  @click="copyToAnotherAccounts('proxy', importDialog.tab2.newAccounts.indexOf(accounts))"
                  v-on="on"
                >
                  <v-icon>clear_all</v-icon>
                </v-btn>
              </template>
              <span>{{ $t('adsmanager.accounts.add.copyToAnotherAccounts') }}</span>
            </v-tooltip>
          </template>
        </v-autocomplete>
      </template>
      
      <template #[`item.tags`]="{item: accounts}">
        <!-- ТЕГИ -->
        <v-combobox
          v-model="accounts.tags"
          :label="$t('common.tags')"
          :items="tags.all"
          class="my-2"
          single-line
          :dense="!accounts.tags || accounts.tags && accounts.tags.length === 0 ? true : false"
          small-chips
          multiple 
          chips
          deletable-chips
          clearable
          outlined
          hide-details
        >
          <!-- ШАБЛОН КНОПКИ СКОПИРОВАТЬ -->
          <template #append-outer>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn
                  icon
                  x-small
                  style="margin-top: 2px;"
                  @click="copyToAnotherAccounts('tags', importDialog.tab2.newAccounts.indexOf(accounts))"
                  v-on="on"
                >
                  <v-icon>clear_all</v-icon>
                </v-btn>
              </template>
              <span>{{ $t('adsmanager.accounts.add.copyToAnotherAccounts') }}</span>
            </v-tooltip>
          </template>
        </v-combobox>
      </template>

      <!-- COOKIES -->
      <template #[`item.cookies`]="{item: account}">
        <v-textarea
          :value="isJsonString(account.cookies) ? account.cookies : null"
          :label="$t('common.cookies')"
          :rows="1"
          outlined
          dense
          :spellcheck="false"
          hide-details
          class="textarea-with-info-button mr-2"
          @input="account.cookies = $event"
        />
      </template>

      <!-- ПАРОЛЬ -->
      <template #[`item.password`]="{item: account}">
        <v-text-field
          v-model="account.password"
          :label="$t('common.password')"
          outlined
          dense
          hide-details
          class="mr-2"
        />
      </template>
    </v-data-table>
  </div>
</template>
<script>
import useragents      from '@/constants/useragents';
import {mapGetters}    from 'vuex';

export default {
  name: 'AccountsDialogBulkAddDataCheck',

  data() {
    return {
      data: [],
      currentAccount: null,
      useragents
    };
  },

  computed: {
    ...mapGetters({
      proxy: 'proxy/proxy',
      tags: 'tags/tags',
      importDialog: 'accounts_v2/importDialog'
    }),

    cols() {
      let cols = [];

      cols.push({text: this.$t('common.name'), value: 'name'});
      cols.push({text: this.$t('common.access_token'), value: 'access_token', width: 250, sortable: false});
      cols.push({text: this.$t('common.useragent'), value: 'useragent', width: 250, sortable: false});
      cols.push({text: this.$t('common.proxy'), value: 'proxy', width: 250, sortable: false});
      cols.push({text: this.$t('common.tags'), value: 'tags', width: 250, sortable: false});
      cols.push({text: this.$t('common.cookies'), value: 'cookies', width: 250, sortable: false});
      cols.push({text: this.$t('common.password'), value: 'password', width: 250, sortable: false});

      return cols;
    }
  },

  methods: {
    setTab2NewAccountsOptions(index, option, value) {
      let data = {
        index: index,
        option: option,
        value: value
      };
      this.$store.dispatch('accounts_v2/setTab2NewAccountsOptions', data);
    },

    searchExistingProxy(item, queryText) {
      const q = queryText.toLowerCase();
      if (typeof item.name !== undefined && item.name && item.name.toString().toLowerCase().search(q) !== -1) return true;
      if (item.ip && item.ip.toString().toLowerCase().search(q) !== -1) return true;
      if (item.port && item.port.toString().toLowerCase().search(q) !== -1) return true;
      if (item.login && item.login.toString().toLowerCase().search(q) !== -1) return true;
      if (item.password && item.password.toString().toLowerCase().search(q) !== -1) return true;

      return false;
    },

    copyToAnotherAccounts(what, index) {
      for (const i in this.importDialog.tab2.newAccounts) {
        this.importDialog.tab2.newAccounts[i][what] = this.importDialog.tab2.newAccounts[index][what];
      }
    },

    randomizeUseragent(index) {
      let max = this.useragents.length;
      let min = 1;
      let randomUseragent = Math.floor(Math.random() * (max - min + 1) + min);
      return this.setTab2NewAccountsOptions(index, 'user_agent', this.useragents[randomUseragent - 1]);
    },

    isJsonString (str) {
      try {
        JSON.parse(str);
      } catch (e) {
        return false;
      }
      return true;
    },
  }
};
</script>
<style>
.data-check-table .v-data-table__wrapper {
  overflow-x: hidden;
}
</style>