<template>
  <td>
    <span
      v-if="show"
    >
      {{ 
        itIsTotalRow ? 
          numeral(stat[col]).format('0,0.[00]').split(',').join(' ') :
          numeral(item['stats'][col]).format('0,0.[00]').split(',').join(' ')
      }}
    </span>
    <span v-else>
      0
    </span>
    <span v-if="col === 'spend'"> {{ currency }}</span>
    <span v-if="col === 'cpm'"> {{ currency }}</span>
    <span v-if="col === 'profit'"> {{ currency }}</span>
    <span v-if="col === 'revenue'"> {{ currency }}</span>
    <span v-if="col === 'roi'">%</span>
    <span v-if="col === 'approve'">%</span>
  </td>
</template>

<script>
import numeral        from 'numeral';
import {mapGetters}   from 'vuex';

export default {
  name: 'AdsManagerSimpleStatCell',

  props: {
    item: {
      type: Object,
      default: () => ({})
    },

    col: {
      type: String,
      default: '',
    },

    itIsTotalRow: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      numeral,
      excludedCols: [
        'billing'
      ]
    };
  },

  computed: {
    ...mapGetters({
      currency: 'adsmanager/currency'
    }),

    stat() {
      return this.$store.getters[`${this.$store.state.adsmanager.activeTab.component}_v2/stat`];
    },
    show() {
      if (!this.stat) return false;
      if (this.excludedCols.indexOf(this.col) > -1) return false;

      if(this.itIsTotalRow) {
        return (
          typeof this.stat !== 'undefined' &&
          typeof this.stat[this.col] !== 'undefined'
        );
      }
                
      return (
        typeof this.item !== 'undefined' &&
        typeof this.item['stats'][this.col] !== 'undefined'
      );
    }
  },
};
</script> 