<template>
  <v-dialog
    :value="dialogs.add"
    persistent
    fullscreen
    scrollable
    @keydown.esc="close"
  >
    <v-card :loading="loading.addDialog">
      <v-toolbar
        :max-height="48"
        color="primary"
        class="white--text dialog-toolbar"
      >
        <v-btn
          color="white"
          icon
          @click="close"
        >
          <v-icon>fas fa-times</v-icon>
        </v-btn>
        <v-toolbar-title>{{ $t('dialogs.accounts.singleAdd.title') }}</v-toolbar-title>
        <v-spacer />
        <v-toolbar-items>
          <v-btn
            color="white"
            text
            :disabled="tabs === 0 ? !validation.state : false"
            :loading="loading.addDialog"
            @click="tabs === 0 ? addAccount() : tabs === 1 ? addBulkAccounts() : addLoginPassword()"
          >
            {{ $t('common.add') }}
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <v-tabs
              v-model="tabs"
              centered
            >
              <!-- ДОБАВИТЬ ОДИН -->
              <v-tab>
                {{ $t('adsmanager.accounts.add.singleAdd') }}
              </v-tab>
              <v-tab-item>
                <dialog-form
                  @change="newAccount = $event"
                  @proxyChange="proxyState = $event"
                  @useragentChange="!initialUseragentChanged ? setInitialUseragent($event) : ''"
                  @validation-changed="validation.state = $event"
                />
              </v-tab-item>
              <!-- ДОБАВИТЬ МНОГО -->
              <v-tab>
                {{ $t('adsmanager.accounts.add.bulkAdd') }}
              </v-tab>
              <v-tab-item>
                <dialog-bulk-add />
              </v-tab-item>
              <!-- ЛОГИН / ПАРОЛЬ -->
              <!-- <v-tab>
                {{ $t('common.login') + " / " + $t('common.password') }}
              </v-tab> -->
              <v-tab-item>
                <dialog-login-password :process-data="tab3" />
              </v-tab-item>
              <!-- ИМПОРТ -->
              <!-- <v-tab>
                {{ $t('adsmanager.accounts.add.import') }}
              </v-tab> -->
              <v-tab-item>
                {{ $t('adsmanager.accounts.add.import') }}
              </v-tab-item>
            </v-tabs>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters}          from 'vuex';
    
import DialogBulkAdd         from './AccountsDialogBulkAdd';
import DialogForm            from './AccountsDialogForm';
import DialogLoginPassword   from './AccountsDialogLoginPassword';

export default {
  name: 'AccountsSingleAddDialog',

  components: {
    DialogBulkAdd,
    DialogLoginPassword,
    DialogForm
  },

  data() {
    return {
      validation: {
        state: false,
      },
      newAccount: {},
      tabs: 0,
      tab3: {
        responses: {},
        loading: {},
      },
      proxyState: false,
      initialUseragent: '',
      initialUseragentChanged: false,
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      loading: 'accounts_v2/loading',
      importDialog: 'accounts_v2/importDialog',
    })
  },

  created() {
    this.$store.dispatch('tags/loadTags');
    this.$store.dispatch('proxy/loadProxy');
  },

  methods: {
    close() {
      if (this.newAccount.access_token !== '' ||
        this.newAccount.name !== '' ||
        this.newAccount.tags.length !== 0 ||
        this.initialUseragent !== this.newAccount.useragent ||
        this.newAccount.useragent === '' ||
        this.newAccount.cookies !== null ||
        this.proxyState ||
        this.importDialog.tab2.dataRaw !== '' ||
        this.importDialog.tab3.dataRaw !== ''
      ) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('accounts_v2/closeDialog', 'add');
    },

    setInitialUseragent(useragent) {
      this.initialUseragent = useragent;
      this.initialUseragentChanged = true;
    },

    addAccount() {
      this.$store.dispatch('accounts_v2/addAccount', this.newAccount);
    },

    async addBulkAccounts() {
      await this.$store.dispatch('accounts_v2/setCreateCabsCount', 0);
      for (const account of this.importDialog.tab2.newAccounts) {
        const clonedAccount = this.$_.cloneDeep(account);

        clonedAccount.cookies = clonedAccount.cookies ? JSON.parse(clonedAccount.cookies) : null;
        
        await this.$store.dispatch('accounts_v2/createBulkAccounts', clonedAccount);
      }
      this.$store.dispatch('accounts_v2/closeDialog', 'add');
    },

    async addLoginPassword() {
      await this.$store.dispatch('accounts_v2/setAddDialogLoading', true);
      this.$set(this.tab3, 'loading', {});
      this.$set(this.tab3, 'responses', {});
      for (const accountIndex in this.importDialog.tab3.newAccounts) {
        this.$set(this.tab3.loading, accountIndex, true);
        const response = await this.api.post('/accounts/add', this.importDialog.tab3.newAccounts[accountIndex]);
        if (
          typeof response.data.data !== 'undefined' &&
          response.data.data.account_id !== 'undefined'
        ) {
          const account_id = response.data.data.account_id;
          let apiMethod = 'get_token_via_emulation';
          switch(this.importDialog.tab3.mode) {
          case 'browser': 
            apiMethod = 'get_token_via_emulation';
            break;
          case 'api': 
            apiMethod = 'get_token_via_api';
            break;
          case 'form':
            apiMethod = 'get_token_via_form';
            break;
          }
          const response2 = await this.api.post('/automator/' + apiMethod, {account_id: account_id});
          this.$set(this.tab3.responses, accountIndex, response2.data);
        }
        this.$set(this.tab3.loading, accountIndex, false);
      }
      await this.$store.dispatch('accounts_v2/setAddDialogLoading', false);
    },
  }
};
</script>