<template>
  <v-dialog
    :value="dialogs.pageAppeal"
    :width="800"
    transition="dialog-bottom-transition"
    scrollable
    @keydown.esc="closeDialog"
    @click:outside="closeDialog"
  >
    <!-- ДИАЛОГ ОШИБКИ -->
    <v-dialog
      v-model="detailsDialog"
      width="700"
      scrollable
    >
      <v-card>
        <v-card-title class="headline">
          {{ $t('common.learnMore') }}
        </v-card-title>

        <v-card-text style="margin-top: 40px;">
          <div class="d-flex justify-end">
            <div style="position: fixed; margin-top: -40px;">
              <v-btn
                color="primary"
                text
                @click="copyToClipboard(JSON.stringify(forDetails))"
              >
                <v-icon
                  :size="14"
                  class="mr-2"
                >
                  fas fa-copy
                </v-icon>
                {{ $t('common.copy') }}
              </v-btn>
            </div>
          </div>
          <code class="mt-0">{{ JSON.stringify(forDetails, null, 2) }}</code>
        </v-card-text>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="detailsDialog = false;"
          >
            {{ $t('common.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-card>
      <!-- ХЕДЕР ДИАЛОГА -->
      <v-toolbar
        color="primary"
        dark
        flat
        dense
      >
        <!-- КНОПКА ЗАКРЫТЬ -->
        <v-btn
          icon
          @click="closeDialog"
        >
          <v-icon>fas fa-times</v-icon>
        </v-btn>

        <!-- ТАЙТЛ -->
        <v-toolbar-title>
          {{ $t('adsmanager.accounts.pageAppeal') }}
        </v-toolbar-title>

        <v-spacer />

        <!-- КНОПКИ ХЭДЕРА -->
        <v-toolbar-items>
          <v-btn
            v-if="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? false : true"
            text
            @click="closeDialog"
          >
            {{ $t('common.cancel') }}
          </v-btn>
          <v-btn
            text
            :disabled="$_.isEmpty(selectedPages)"
            :loading="loading"
            @click="appealPage"
          >
            {{ $t('common.appeal') }}
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      
      <v-card-text>
        <v-data-table
          :headers="colsAccounts"
          fixed-header
          :items="accounts.selected"
        >
          <!-- НАЗВАНИЕ АККАУНТА -->
          <template #[`item.account`]="{item: account}">
            {{ account.name }}
          </template>

          <!-- ФП -->
          <template #[`item.page`]="{item: account}">
            <v-data-table
              :headers="colsPages"
              hide-default-header
              hide-default-footer
              :items="account.pages"
              :items-per-page="-1"
              show-select
              class="accounts-dialog-page-appeal-pages-table"
            >
              <template #[`item.data-table-select`]="{item: page}">
                <v-checkbox
                  :input-value="selectedPages.includes(page)"
                  :ripple="false"
                  :disabled="!pageIsReady(account, page).value"
                  @change="selectPage(page)"
                />
              </template>

              <template #[`item.pageName`]="{item: page}">
                <v-avatar :size="18">
                  <img :src="page.ava">
                </v-avatar>

                {{ page.name }}
              </template>

              <!-- СТАТУС ФП -->
              <template #[`item.pageStatus`]="{item: page}">
                <v-chip
                  :color="pageStatus(page).color"
                  x-small
                  label
                  outlined
                >
                  {{ pageStatus(page).status }}
                </v-chip>
              </template>

              <!-- ПРОГРЕСС-БАР -->
              <template #[`item.jobStatus`]="{item: page}">
                <div v-if="pageIsReady(account, page).value">
                  <v-progress-linear
                    v-if="!responses[page.page_id]"
                    :indeterminate="loadingPages.includes(page.page_id)"
                    :value="0"
                    style="min-width: 50px;"
                  />
              
                  <span v-else-if="responses && responses[page.page_id] && responses[page.page_id]['success']">
                    <v-icon color="green">
                      check_box
                    </v-icon>
                  </span>

                  <span v-else-if="responses && responses[page.page_id] && !responses[page.page_id]['success']">
                    <v-icon color="red">
                      cancel
                    </v-icon>
                    <span
                      style="font-size: 12px; cursor: pointer; color: #2196F3; text-decoration: underline;"
                      @click="openDetails(responses[page.page_id]['response'])"
                    >{{ $t('common.learnMore') }}</span>
                  </span>
                </div>
                
                <span
                  v-else
                  style="color: #9E9E9E;"
                >
                  {{ pageIsReady(account, page).text }}
                </span>
              </template>
            </v-data-table>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import clipboard        from '@/mixins/clipboard';
import {mapGetters}     from 'vuex';

export default {
  name: 'AccountsDialogPageAppeal',

  mixins: [clipboard],

  data() {
    return {
      detailsDialog: false,
      forDetails: [],
      loading: false,
      selectedPages: [],
      responses: {},
      loadingPages: []
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/accounts',
      dialogs: 'accounts_v2/dialogs'
    }),

    colsAccounts() {
      let cols = [];

      cols.push({text: this.$t('common.account'), value: 'account', width: 150});
      cols.push({text: this.$t('common.fanPage'), value: 'page'});

      return cols;
    },

    colsPages() {
      let cols = [];

      cols.push({text: this.$t('settings.adsCreation.settingsPage.pageName'), value: 'pageName', width: 150});
      cols.push({text: this.$t('common.status'), value: 'pageStatus', width: 150, sortable: false});
      cols.push({text: this.$t('common.jobStatus'), value: 'jobStatus', sortable: false});

      return cols;
    },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('accounts_v2/closeDialog', 'pageAppeal');
    },

    pageIsReady(account, page) {
      if (account && account.status !== 'ACTIVE') {
        return {
          text: this.$t('common.errorMessages.accountIsNotActive'), 
          value: false
        };
      }
      if (account && this.$_.isEmpty(account.cookies)) {
        return {
          text: this.$t('common.errorMessages.accountHasNoCookies'), 
          value: false
        };
      }
      if (page.advertising_restriction !== undefined && parseInt(page.advertising_restriction, 10) === 0) {
        return {
          text: this.$t('common.errorMessages.pageIsNotBanned'),
          value: false
        };
      }

      return {value: true};
    },

    async appealPage() {
      try {
        let pageAppealed = false;
        this.loading = true;
        this.responses = {},
        this.loadingPages = [];

        for (const page of this.selectedPages) {
          this.loadingPages.push(page.page_id);
        }
      
        for (const page of this.selectedPages) {          
          const response = await this.api.post(`/pages/appeal_ban/?account_id=${page.account_id}&page_id=${page.page_id}`);

          if (response.status === 200 && response.data.result) {
            if (!pageAppealed) pageAppealed = true;

            this.$set(this.responses, page.page_id, { success: true });
            this.selectedPages = this.selectedPages.filter(i => parseInt(i.page_id, 10) !== parseInt(page.page_id, 10));
            this.loadingPages = this.loadingPages.filter(i => parseInt(i.page_id, 10) !== parseInt(page.page_id, 10));
          } else {
            this.$set(this.responses, page.page_id, { success: false, response: response });
          }
        }

        if (pageAppealed) this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
      } catch (err) {
        // Do nothing
      } finally {
        this.loading = false;
        this.loadingPages = [];
      }
    },

    openDetails(response) {
      this.forDetails = response;
      this.detailsDialog = true;
    },

    selectPage(page) {
      if (this.selectedPages.includes(page)) {
        this.selectedPages = this.selectedPages.filter(i => parseInt(i.page_id, 10) !== parseInt(page.page_id, 10));
      } else {
        this.selectedPages.push(page);
      }
    },

    pageStatus(page) {
      if (page.advertising_restriction !== undefined && parseInt(page.advertising_restriction, 10) === 1) {
        return { status: 'banned', color: 'error'};
      }

      if (page.is_published !== undefined && parseInt(page.is_published, 10) === 0) {
        return { status: 'unpublished', color: 'error'};
      }

      return { status: 'active', color: 'success' };
    }
  }
};
</script>

<style>
.accounts-dialog-page-appeal-pages-table tbody tr:last-child td {
  border-bottom: none !important;
}
.accounts-dialog-page-appeal-pages-table tbody tr td:first-child {
  width: 40px;
}
.accounts-dialog-page-appeal-pages-table tbody tr td:nth-child(2) {
  width: 150px;
}
.accounts-dialog-page-appeal-pages-table tbody tr td:nth-child(3) {
  width: 150px;
}
</style>