<template>
  <div>
    <v-chip
      v-for="tag in account.tags"
      :key="`account-${account.id}-tag-${tag}`"
      class="mr-1"
      :color="detectColor(tag)"
      outlined
      x-small
      label
    >
      {{ tag }}
    </v-chip>
  </div>
</template>

<script>
export default {
  name: 'AccountsMainTableTags',
  
  props: {
    account: {
      type: Object,
      default: () => ({})
    }
  },

  methods: {
    detectColor(tag) {
      if (tag === 'no-updates' || tag === 'не-обновлять' || tag === 'без-комментов' || tag === 'no-comments') return 'red';
      return 'primary';
    }
  }
};
</script>