<template>
  <v-combobox
    label="Валюта"
    :items="currencies"
    :value="currency"
    solo
    hide-details
    :height="30"
    dense
    @change="currencyChange"
  />
</template>

<script>
import currencies   from '@/constants/currencies.js';
import {mapGetters} from 'vuex';

export default {
  name: 'AdsManagerCurrencySelectorV2',

  data: function() {
    return ({
      currencies
    });
  },

  computed: {
    ...mapGetters({
      currency: 'adsmanager/currency'
    })
  },

  methods: {
    currencyChange(currency) {
      this.$store.dispatch('adsmanager/changeCurrency', currency);
      this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
    }
  }
};
</script>