<template>
  <td>
    <span
      v-if="show"
    >
      <span v-if="col === 'action_results'">
        {{
          itIsTotalRow ?
            numeral(stat['results']).format('0,0.[00]').split(',').join(' ') :
            numeral(item['results']).format('0,0.[00]').split(',').join(' ')
        }}
      </span>
      <span v-else>
        {{
          itIsTotalRow ?
            numeral(stat['actions'][col]).format('0,0.[00]').split(',').join(' ') :
            numeral(item['stat_actions'][col]).format('0,0.[00]').split(',').join(' ')
        }}
      </span>
    </span>
    <span v-else>
      0
    </span>
  </td>
</template>

<script>
import numeral        from 'numeral';

export default {
  name: 'AdsManagerComplexStatCellQuantity',

  props: {
    item: {
      type: Object,
      default: () => ({})
    },
    col: {
      type: String,
      default: '',
    },
    itIsTotalRow: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    return {
      numeral
    };
  },

  computed: {
    stat() {
      return this.$store.getters[`${this.$store.state.adsmanager.activeTab.component}_v2/stat`];
    },

    show() {
      if (!this.stat) return false;
      
      if (this.col === 'action_results') {
        if(this.itIsTotalRow) {
          return this.stat['results'] !== 'undefined';
        }

        return typeof this.item['results'] !== 'undefined';
      }

      if(this.itIsTotalRow) {
        return (
          typeof this.stat['actions'] !== 'undefined' &&
          typeof this.stat['actions'][this.col] !== 'undefined'
        );
      }

      return this.item['stat_actions'] && (this.col in this.item['stat_actions']);
    }
  },
};
</script>