<template>
  <v-dialog
    :value="dialogs.edit"
    persistent
    fullscreen
    @keydown.esc="close"
  >
    <v-card :loading="loading.editDialog">
      <v-toolbar
        color="primary"
      >
        <v-btn
          icon
          @click="close"
        >
          <v-icon>fas fa-times</v-icon>
        </v-btn>
        <v-toolbar-title>{{ $t('dialogs.accounts.edit.title', {accountName: account.name}) }}</v-toolbar-title>
        <v-spacer />
        <v-toolbar-items>
          <v-btn
            text
            :disabled="!validation.state"
            @click="saveAccount"
          >
            {{ $t('common.save') }}
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-card-text>
        <dialog-form
          edit
          :account="account"
          @change="changeAccount($event)"
          @validation-changed="validation.state = $event"
        />
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

import DialogForm   from './AccountsDialogForm';

export default {
  name: 'AccountsSingleEditDialog',

  components: {
    DialogForm
  },

  data() {
    return {
      validation: {
        state: false,
      },
      newAccount: {},
      initialAccount: {},
      initialAccountChanges: false,
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      loading: 'accounts_v2/loading',
      account: 'accounts_v2/forEdit'
    })
  },

  methods: {
    close() {
      if (!this.$_.isEqual(this.newAccount, this.initialAccount)) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }
      
      this.$store.dispatch('accounts_v2/closeDialog', 'edit');
    },

    changeAccount(account) {
      this.newAccount = account;

      if (!this.initialAccountChanges) {
        this.initialAccount = this.$_.cloneDeep(account);
        this.initialAccountChanges = true;
      } 
    },

    saveAccount() {
      this.$store.dispatch('accounts_v2/saveAccount', this.newAccount);
    }
  }
};
</script>
