<template>
  <v-list-item
    dense
    @click="archive"
  >
    <v-list-item-title>{{ $t('common.archive') }}</v-list-item-title>
  </v-list-item> 
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'AccountsDialogArchive',

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/accounts'
    })
  },

  methods: {
    archive() {
      if (confirm(this.$t('common.confirmPlease'))) {
        const data = {
          ids: this.accounts.selected.map(account => account.id)
        };
        this.$store.dispatch('accounts_v2/archive', data);
      }
    }
  }
};
</script>