<template>
  <div
    class="my-2"
  >
    <div>
      <div :style="$vuetify.breakpoint.xs ? 'display: flex; align-items: center;' : ''">
        <v-btn
          v-if="$vuetify.breakpoint.xs"
          x-small
          icon
          class="mr-1"
          @click.stop.prevent="openMainInfo"
        >
          <v-icon :size="16">
            fas fa-info-circle
          </v-icon>
        </v-btn>
        <span
          v-if="!$vuetify.breakpoint.xs"
          style="color: grey;"
        >
          [{{ account.id }}]
        </span>

        <v-icon
          v-if="!$vuetify.breakpoint.xs"
          :size="12"
          style="cursor: pointer; margin-right: 4px;"
          :title="$t('common.fbApiLog')"
          @click.stop.prevent="loadLog(account)"
        >
          fas fa-book
        </v-icon>

        <!-- ИКОНКА СИНХРОНИЗАЦИИ АККАУНТА -->
        <v-tooltip
          v-if="!$vuetify.breakpoint.xs && account.is_syncing_now"
          top
        >
          <template v-slot:activator="{ on }">
            <v-icon
              :size="12"
              color="primary"
              class="mr-1"
              v-on="on"
            >
              fas fa-spinner fa-pulse
            </v-icon>
          </template>
          <span>{{ $t('common.syncIconTooltip') }}</span>
        </v-tooltip>

        <!-- НАЗВАНИЕ АККАУНТА -->
        <span
          style="cursor: pointer; color: #2196F3; text-decoration: underline; display: inline-block; max-width: 150px;"
          @click.stop.prevent="editAccount(account)"
        >
          {{ account.name }}
        </span>
      </div>

      <!-- АРХИВИРОВАННЫЕ -->
      <div v-if="account.archived == 1">
        <v-chip
          class="mr-1"
          x-small
        >
          {{ $t('common.archived') }}
        </v-chip>
      </div>
      <div
        v-if="!$vuetify.breakpoint.xs && !account.fb_id"
        style="width: 90%; margin-top: 4px;"
      >
        <v-alert
          text
          color="error"
          style="padding: 0 5px; font-size: 10px; line-height: 17px;"
        >
          {{ $t('adsmanager.accounts.noFacebookId') }}
          <a
            style="font-size: 10px;"
            @click.stop.prevent="$store.dispatch('accounts_v2/openDialog', 'noFacebookIdInfo')"
          >
            {{ $t('common.whatsThat') }}
          </a>
        </v-alert>
      </div>
      <div
        v-if="!$vuetify.breakpoint.xs"
        style="font-size: 11px; color: gray;"
      >
        <span
          class="d-block"
          style="line-height: 14px;"
        >
          {{ $t('common.owner') }}:
          {{
            account.user
              ? account.user.display_name ? account.user.display_name : account.user.login
              : '-'
          }}
        </span>

        <span
          class="d-block"
          style="line-height: 14px;"
        >
          {{ $t('adsmanager.accounts.rulesAccepted') }}:
          <v-icon
            v-if="account.rules_accepted"
            :size="12"
            color="success"
          >
            fas fa-check
          </v-icon>
          <v-icon
            v-else
            :size="12"
            color="error"
          >
            fas fa-times
          </v-icon>
        </span>
      </div>
      <div
        v-if="!$vuetify.breakpoint.xs && account.permissions"
        class="caption"
      >
        <a @click.stop.prevent="showPermissions">
          {{ $t('adsmanager.accounts.shared').toLowerCase() }} ({{ account.permissions.length }})
        </a>
      </div>
    </div>
    <account-tags :account="account" />
  </div>
</template>

<script>
import AccountTags                      from './AccountsMainTableTags';

export default {
  name: 'AccountsMainTableInfo',

  components: {
    AccountTags,
  },

  props: {
    account: {
      type: Object,
      default: () => ({})
    }
  },

  methods: {
    async openMainInfo() {
      await this.$store.dispatch('accounts_v2/setForDialogMainInfo', this.account);
      await this.$store.dispatch('accounts_v2/openDialog', 'mainInfo');
    },

    editAccount(account) {
      this.$store.dispatch('accounts_v2/initAccountForEdit', this.$_.cloneDeep(account));
    },

    async loadLog(account) {
      this.$store.dispatch('fbApiLog/clearLog');
      this.$store.dispatch('fbApiLog/openDialog', 'logSidebar');
      this.$store.dispatch('fbApiLog/loadLog', [{object: 'account_id', id: account.id}]);
    },

    async showPermissions() {
      await this.$store.dispatch('accounts_v2/setForPermissions', this.account);
      await this.$store.dispatch('accounts_v2/openDialog', 'permissions');
    }
  }
};
</script>
