<template>
  <v-dialog
    :value="dialogs.changeProxy"
    width="500"
    :overlay-opacity="0.9"
    scrollable
    @click:outside="close"
    @keydown.esc="close"
  >
    <v-card>
      <!-- ЗАГОЛОВОК -->
      <v-card-title>
        <div style="display: flex; justify-content: space-between; width: 100%;">
          <div>
            <span class="headline">
              {{ $t('proxy.changeProxy') }}
            </span>
          </div>
        </div>
      </v-card-title>

      <v-card-text>
        <v-row>
          <!-- СУЩЕСТВУЮЩИЙ ПРОКСИ -->
          <v-col cols="12">
            <v-autocomplete
              v-model="existingProxy"
              :items="proxy.all.concat(newProxies)"
              :label="$t('common.existingProxy')"
              :item-value="item => item.id"
              :filter="searchExistingProxy"
              clearable
              outlined
              dense
              hide-details
            >
              <template v-slot:item="{item}">
                [{{ item.accounts_count }}]
                {{
                  typeof item.name !== 'undefined' && item.name !== null ?
                    item.name :
                    item.login === null ?
                      `${item.ip}:${item.port}` :
                      `${item.ip}:${item.port}:${item.login}:${item.password}`
                }}
              </template>
              <template v-slot:selection="{item}">
                [{{ item.accounts_count }}]
                {{
                  typeof item.name !== 'undefined' && item.name !== null ?
                    item.name :
                    item.login === null ?
                      `${item.ip}:${item.port}` :
                      `${item.ip}:${item.port}:${item.login}:${item.password}`
                }}
              </template>
            </v-autocomplete>
          </v-col>
          <!-- БЫСТРЫЙ ВВОД -->
          <v-col cols="12">
            <v-text-field
              v-model="quickInput"
              :label="$t('adsmanager.accounts.add.quickInput')"
              outlined
              dense
              hide-details
            />
          </v-col>
          <!-- НАЗВАНИЕ ПРОКСИ -->
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="parsedProxy.name"
              :label="$t('common.name')"
              outlined
              dense
              hide-details
            />
          </v-col>
          <!-- ТИП ПРОКСИ -->
          <v-col
            cols="12"
            sm="6"
          >
            <v-select
              v-model="parsedProxy.type"
              :label="$t('common.type')"
              :items="proxy_types"
              outlined
              dense
              hide-details
            />
          </v-col>
          <!-- ХОСТ ПРОКСИ -->
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="parsedProxy.ip"
              :label="$t('common.host')"
              outlined
              dense
              hide-details
            />
          </v-col>
          <!-- ПОРТ ПРОКСИ -->
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="parsedProxy.port"
              :label="$t('common.port')"
              outlined
              dense
              hide-details
            />
          </v-col>
          <!-- ЛОГИН ПРОКСИ -->
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="parsedProxy.login"
              :label="$t('common.login')"
              outlined
              dense
              hide-details
            />
          </v-col>
          <!-- ПАРОЛЬ ПРОКСИ -->
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="parsedProxy.password"
              :label="$t('common.password')"
              outlined
              dense
              hide-details
            />
          </v-col>
        </v-row>
      </v-card-text>

      <v-divider />

      <!-- КНОПКИ ДИАЛОГА -->
      <v-card-actions>
        <v-spacer />

        <v-btn
          color="blue darken-1"
          text
          @click="close"
        >
          {{ $t('common.close') }}
        </v-btn>

        <v-btn
          color="blue darken-1"
          text
          :loading="loading"
          :disabled="disable"
          @click="changeProxy"
        >
          {{ $t('common.save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters } from 'vuex';

import proxy_types    from '../../../constants/proxy/proxy_types';

export default {
  name: 'AccountsDialogChangeProxy',

  data () {
    return {
      proxy_types,
      parsedProxy: {},
      existingProxy: {},
      loading: false,
      newProxies: [],
      quickInput: ''
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'accounts_v2/dialogs',
      accounts: 'accounts_v2/accounts',
      proxy: 'proxy/proxy'
    }),

    disable () {
      let disabled = true;
      if (
        typeof this.parsedProxy.type !== 'undefined' && this.parsedProxy.type !== '' &&
          typeof this.parsedProxy.ip !== 'undefined' && this.parsedProxy.ip !== '' &&
          typeof this.parsedProxy.port !== 'undefined' && this.parsedProxy.port !== ''
      ) {
        disabled = false;
      }
      if (
        typeof this.parsedProxy.type !== 'undefined' && this.parsedProxy.type !== '' &&
          typeof this.parsedProxy.ip !== 'undefined' && this.parsedProxy.ip !== '' &&
          typeof this.parsedProxy.port !== 'undefined' && this.parsedProxy.port !== '' &&
          typeof this.parsedProxy.login !== 'undefined' && this.parsedProxy.login !== '' &&
          typeof this.parsedProxy.password !== 'undefined' && this.parsedProxy.password === ''
      ) {
        disabled = true;
      }

      return disabled;
    }
  },

  watch: {
    quickInput: function() {
      this.parse();
    },

    existingProxy: function() {
      this.loadDataFromExistingProxy();
    },
  },

  created() {
    this.$store.dispatch('proxy/loadProxy');
  },

  methods: {
    close () {
      this.$store.dispatch('accounts_v2/closeDialog', 'changeProxy');
    },

    changeProxy () {
      for (let account of this.accounts.selected) {
        const data = {...account};
        data.proxy = {
          id: this.parsedProxy.id,
          ip: this.parsedProxy.ip,
          login: this.parsedProxy.login,
          name: this.parsedProxy.name,
          password: this.parsedProxy.password,
          port: this.parsedProxy.port,
          type: this.parsedProxy.type
        };
        data.proxy_id = this.parsedProxy.id;

        this.$store.dispatch('accounts_v2/saveAccount', data);
      }
      this.close();
    },

    searchExistingProxy(item, queryText) {
      const q = queryText.toLowerCase();
      if (typeof item.name !== undefined && item.name && item.name.toString().toLowerCase().search(q) !== -1) return true;
      if (item.ip && item.ip.toString().toLowerCase().search(q) !== -1) return true;
      if (item.port && item.port.toString().toLowerCase().search(q) !== -1) return true;
      if (item.login && item.login.toString().toLowerCase().search(q) !== -1) return true;
      if (item.password && item.password.toString().toLowerCase().search(q) !== -1) return true;

      return false;
    },

    loadDataFromExistingProxy() {
      if (typeof this.existingProxy === 'undefined') {
        this.parsedProxy = {
          name: '',
          type: '',
          ip: '',
          port: '',
          login: '',
          password: '',
        };
        this.quickInput = null;
      }
      const proxy = this.proxy.all.find(p => p.id === this.existingProxy);
      if (proxy) {
        this.parsedProxy = {...proxy};
      }
    },

    parse() {
      if (this.quickInput === null || this.quickInput === '') return;
      this.newProxies = [];
      let data = {
        proxy: {
          id: null,
          ip: null,
          port: null,
          login: null,
          password: null,
          name: null,
          accounts_count: 0,
          type: null
        },
        newProxy: null,
      };

      let proxy = this.quickInput.trim();

      if (proxy.indexOf(':') === -1) {
        let existingProxy = this.proxy.all.find(p => p.id === parseInt(proxy));
        data.proxy = {...existingProxy};
        data.newProxy = null;
      } else {
        data.newProxy = this.quickInput.trim();
        let proxyType = null;
        if (proxy.indexOf('/') === -1) {
          proxyType = 'http';
        } else {
          if (proxy.split('://').length > 1) {
            proxyType = proxy.split('://')[0].toLowerCase();
            if (proxyType === 'https') proxyType = 'http';
            if (proxyType === 'socks') proxyType = 'socks5';
            proxy = proxy.split('://')[1];
          }

        }
        if (proxy.split(':').length > 1) {
          if (proxy.split(':').length === 2) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: null,
              password: null,
              name: null,
              accounts_count: 0,
              type: proxyType,
            };
          }
          if (proxy.split(':').length === 3) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: null,
              password: null,
              name: proxy.split(':')[2].trim(),
              accounts_count: 0,
              type: proxyType,
            };
          }
          if (proxy.split(':').length === 4) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: proxy.split(':')[2].trim(),
              password: proxy.split(':')[3].trim(),
              name: null,
              accounts_count: 0,
              type: proxyType,
            };
          }
          if (proxy.split(':').length === 5) {
            data.proxy = {
              id: null,
              ip: proxy.split(':')[0].trim(),
              port: proxy.split(':')[1].trim(),
              login: proxy.split(':')[2].trim(),
              password: proxy.split(':')[3].trim(),
              name: proxy.split(':')[4].trim(),
              accounts_count: 0,
              type: proxyType,
            };
          }
        }
      }
      if (data.proxy.id === null && data.proxy.ip !== null && data.proxy.port !== null) {
        this.newProxies.push({...data.proxy});
        this.existingProxy = {...data.proxy};
        this.parsedProxy = {...data.proxy};
      }
    },
  }
};
</script>
