<template>
  <div>
    <div>
      <v-dialog
        v-model="detailsDialog"
        width="700"
        scrollable
      >
        <v-card>
          <v-card-title class="headline">
            {{ $t('common.learnMore') }}
          </v-card-title>

          <v-card-text>
            <code>{{ JSON.stringify(forDetails) }}</code>
          </v-card-text>

          <v-divider />

          <v-card-actions>
            <v-spacer />
            <v-btn
              color="primary"
              text
              @click="detailsDialog = false;"
            >
              {{ $t('common.close') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <!-- ИНДИКАТОР ЗАГРУЗКИ -->
    <v-dialog
      :value="showLoading"
      hide-overlay
      persistent
      width="300"
      content-class="loading-modal"
    >
      <v-card
        color="primary"
        class="pr-4"
      >
        <v-card-text>
          <div style="display: flex; justify-content: space-between; align-items: center; min-height: 40px; color: white;">
            <div>
              {{ $t('dialogs.accounts.createFanPage.loadingText') }}
            </div>
            <div>
              {{ createFanPageLoading.done }} / {{ createFanPageLoading.accounts.length }}
            </div>
          </div>

          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          />

          <v-btn
            icon
            style="position: absolute; right: 2px; top: 2px;"
            small
            @click="$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'stopRequest', value: true});"
          >
            <v-icon
              :size="14"
              color="white"
            >
              fas fa-times-circle
            </v-icon>
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
    <!-- МОДАЛЬНОЕ ОКНО -->
    <v-dialog
      v-if="showModal"
      :value="dialogs.createFanPage"
      persistent
      fullscreen
      @keydown.esc="close"
    >
      <v-card>
        <v-toolbar
          color="primary"
        >
          <!-- КНОПКА ЗАКРЫТЬ -->
          <v-btn
            icon
            @click="close"
          >
            <v-icon class="white--text">
              fas fa-times
            </v-icon>
          </v-btn>

          <!-- ЗАГОЛОВОК -->
          <v-toolbar-title class="white--text">
            {{ $t('dialogs.accounts.createFanPage.title') }}
          </v-toolbar-title>
          <v-spacer />

          <!-- КНОПКА СОЗДАТЬ -->
          <v-toolbar-items>
            <v-btn
              text
              color="white"
              @click="createFanPages"
            >
              {{ $t('common.create') }}
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>

        <!-- ОСНОВНОЙ КОНТЕЙНЕР -->
        <v-card-text>
          <v-container fluid>
            <!-- МАССОВЫЕ ДЕЙСТВИЯ -->
            <v-row
              align="center"
            >
              <!-- МАССОВЫЙ ВВОД НАЗВАНИЯ -->
              <v-col
                cols="12"
                md="4"
              />

              <!-- МАССОВЫЙ ВЫБОР КАТЕГОРИИ -->
              <v-col
                cols="12"
                md="4"
              />
            </v-row>

            <!-- СТРАНИЦЫ ДЛЯ АККАУНТОВ -->
            <v-row>
              <v-col
                cols="12"
                md="3"
              >
                <v-alert
                  dense
                  text
                  color="info"
                >
                  <span
                    class="caption"
                    v-html="$t('dialogs.accounts.createFanPage.textHint')"
                  />
                </v-alert>
                <v-textarea
                  v-model="multiple.name"
                  :label="$t('common.names')"
                  dense
                  outlined
                  single-line
                  hide-details
                  class="adsmanager-accounts-create-fp-name-textarea"
                >
                  <template
                    #prepend-inner
                  >
                    <v-btn
                      x-small
                      icon
                      @click="generateFpNames"
                    >
                      <v-icon style="margin-top: 3px;">
                        autorenew
                      </v-icon>
                    </v-btn>
                  </template>
                </v-textarea>

                <v-autocomplete
                  v-model="multiple.categoryId"
                  class="mt-5"
                  dense
                  single-line
                  outlined
                  :label="$t('dialogs.accounts.createFanPage.sharedCategoryId')"
                  :items="pageCategories"
                  item-value="id"
                  item-text="name"
                  hide-details
                />

                <v-switch
                  v-model="multiple.turnOffNotifications"
                  :label="$t('common.turnOffNotifications')"
                  hide-details
                />

                <!-- ЗАГРУЗИТЬ МНОГО АВАТАРОВ -->
                <v-btn
                  class="mt-5"
                  block
                  color="primary"
                  :loading="loadAvatars"
                  @click="openAvatarFileSelect"
                >
                  {{ $t('dialogs.accounts.createFanPage.uploadAvatars') }}
                </v-btn>
                <!-- СКРЫТЫЙ ФАЙЛОВЫЙ ИНПУТ -->
                <input
                  id="upload-avatars"
                  ref="upload-avatars"
                  style="display: none;"
                  type="file"
                  multiple
                  @change="handleMultipleAvatarUpload"
                >
              </v-col>
              <v-col
                cols="12"
                md="9"
              >
                <v-row
                  v-for="(account, index) in accountsToProcess"
                  :key="`account-for-fp-creation-${account.id}`"
                  align="center"
                >
                  <!-- АВАТАРКА -->
                  <v-col
                    cols="12"
                    md="3"
                  >
                    <div style="float: left;">
                      <v-avatar class="elevation-3">
                        <img
                          :src="account.uploaded_icon ? `${baseUrl}${account.uploaded_icon}` : account.icon ? `${baseUrl}${account.icon}` : ''"
                        >
                      </v-avatar>
                    </div>
                    <div style="float: left;">
                      <div>
                        <v-btn
                          class="ml-3"
                          text
                          color="primary"
                          x-small
                          @click="changeIcon(index)"
                        >
                          {{ $t('common.change') }}
                        </v-btn>
                      </div>
                      <div>
                        <input
                          :id="`file${index}`"
                          :ref="`file${index}`"
                          style="display: none;"
                          type="file"
                          @change="handleSingleAvatarUpload(index)"
                        >
                        <v-btn
                          class="ml-3"
                          text
                          color="primary"
                          x-small
                          @click="openFileSelect(index)"
                        >
                          {{ $t('dialogs.accounts.createFanPage.uploadCustom.uploadCustom') }}
                        </v-btn>
                      </div>
                    </div>
                  </v-col>

                  <!-- НАЗВАНИЕ СТРАНИЦЫ -->
                  <v-col
                    cols="12"
                    md="3"
                  >
                    <v-text-field
                      v-model="accountsToProcess[index].pageName"
                      :label="$t('dialogs.accounts.createFanPage.fanPageName')"
                      hide-details
                      dense
                      single-line
                      outlined
                    >
                      <!-- КНОПКА СПИНТАКСА -->
                      <template #prepend-inner>
                        <v-btn
                          x-small
                          icon
                          @click="spinOrGenerate(index)"
                        >
                          <v-icon style="margin-top: 3px;">
                            autorenew
                          </v-icon>
                        </v-btn>
                      </template>
                    </v-text-field>
                  </v-col>

                  <!-- КАТЕГОРИЯ СТРАНИЦЫ -->
                  <v-col
                    cols="12"
                    md="2"
                  >
                    <v-autocomplete
                      v-model="accountsToProcess[index].pageCategoryId"
                      hide-details
                      dense
                      single-line
                      outlined
                      :label="$t('dialogs.accounts.createFanPage.fanPageCategory')"
                      :items="pageCategories"
                      item-value="id"
                      item-text="name"
                    />
                  </v-col>

                  <!-- НАЗВАНИЕ АККАУНТА -->
                  <v-col
                    cols="12"
                    md="2"
                  >
                    <div class="d-flex align-center">
                      <span
                        class="mr-2"
                        style="font-size: 18px;"
                      >
                        &rarr;
                      </span>
                      <div>
                        <span class="title">{{ account.name }}</span>
                        <v-alert
                          v-if="!account.cookies || (!Array.isArray(account.cookies) || account.cookies.length === 0)"
                          dense
                          text
                          color="warning"
                          style="font-size: 12px; font-weight: 400;"
                        >
                          {{ $t('adsmanager.accounts.dialogs.createFp.noCookies') }}
                        </v-alert>
                      </div>
                    </div>
                  </v-col>

                  <!-- СТАТУС -->
                  <v-col
                    cols="12"
                    md="2"
                  >
                    <v-progress-linear
                      v-if="loadingAccount(account.id) === undefined || !loadingAccount(account.id).status"
                      :indeterminate="loadingAccount(account.id) && !loadingAccount(account.id).status"
                      value="0"
                    />
                    <span
                      v-if="loadingAccount(account.id) &&
                        loadingAccount(account.id).response &&
                        loadingAccount(account.id).response.data &&
                        loadingAccount(account.id).response.data.page_create &&
                        loadingAccount(account.id).response.data.page_create.page &&
                        loadingAccount(account.id).response.data.page_create.page.id"
                    >
                      <v-icon color="green">check_box</v-icon>
                    </span>
                    <span
                      v-if="loadingAccount(account.id) &&
                        loadingAccount(account.id).response &&
                        loadingAccount(account.id).response.data &&
                        loadingAccount(account.id).response.data.page_create &&
                        loadingAccount(account.id).response.data.page_create.length === 0"
                    >
                      <v-icon color="red">cancel</v-icon>
                      <span
                        style="font-size: 12px; cursor: pointer; color: #2196F3; text-decoration: underline;"
                        @click="openDetails(loadingAccount(account.id).response)"
                      >
                        {{ $t('common.learnMore') }}
                      </span>
                    </span>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import pageCategories            from '@/constants/pages/categories';
import generate                  from 'project-name-generator';
import ucfirst                   from 'ucfirst';
// import axios                     from 'axios';
import {mapGetters}              from 'vuex';

export default {
  name: 'AccountsDialogCreateFanPage',

  data() {
    return {
      validation: {
        state: false,
      },
      newAccount: {},
      pageCategories,
      accountsToProcess: [],
      multiple: {
        name: '',
        categoryId: null,
        turnOffNotifications: true
      },
      showLoading: false,
      showModal: true,
      done: 0,
      icons: [],
      F: [],
      localAvatars: [],
      loadAvatars: false,
      accountWithSpintax: [],
      detailsDialog: false,
      forDetails: {},
      initialAccountsToProcess: [],
    };
  },

  computed: {
    ...mapGetters({
      accounts: 'accounts_v2/ACCOUNTS',
      dialogs: 'accounts_v2/dialogs',
      loading: 'accounts_v2/loading',
      createFanPageLoading: 'accounts_v2/createFanPageLoading',
      activeTab: 'adsmanager/activeTab'
    })
  },

  watch: {
    'multiple.categoryId': {
      handler(newVal) {
        this.accountsToProcess = this.accountsToProcess.map(account => {
          account.pageCategoryId = newVal;
          return account;
        });
      }
    },

    'multiple.name': {
      handler() {
        this.parseName();
      }
    },

    'createFanPageLoading.stopRequest': {
      handler() {
        if (this.createFanPageLoading.stopRequest) {
          for (let request of this.createFanPageLoading.source) {
            request.cancel();
          }
          this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'showLoading', value: false});
        }
      }
    }
  },

  async created() {
    await this.loadIcons();
    this.accountsToProcess = this.$_.cloneDeep(this.accounts.selected);

    this.accountsToProcess = this.accountsToProcess.map((account, index) => {
      account.icon = typeof this.icons[index] !== 'undefined' ? this.icons[index] : false;
      account.uploaded_icon = false;
      account.pageCategoryId = '2700';
      return account;
    });

    this.initialAccountsToProcess = this.$_.cloneDeep(this.accountsToProcess);
  },

  methods: {
    loadingAccount(id) {
      return this.createFanPageLoading.accounts.find(item => parseInt(item.accountId, 10) === parseInt(id, 10));
    },

    async parseName() {
      const lines = this.multiple.name.split('\n');
      if (lines.length > 1) {
        let lineIndex = 0;
        for (let accountIndex in this.accountsToProcess) {
          let account = this.accountsToProcess[accountIndex];
          if (lines[lineIndex] !== '' && lines[lineIndex].search(/\{[^\\{\\}]*\}/) > -1) {
            if (this.accountWithSpintax.indexOf(parseInt(accountIndex)) === -1) {
              this.accountWithSpintax.push(parseInt(accountIndex));
            }
            let spintaxTexts = [];
            let data = {
              text: window.btoa(unescape(encodeURIComponent(lines[lineIndex])))
            };
            let response = await this.api.post('/spintax', data);
            await spintaxTexts.push(response.data.data);
            account.pageName = spintaxTexts[0];
            this.$set(this.accountsToProcess, accountIndex, account);
          } else {
            if (this.accountWithSpintax.length >= 1 && this.accountWithSpintax.indexOf(parseInt(accountIndex)) > -1) {
              this.accountWithSpintax = this.accountWithSpintax.filter(item => item !== parseInt(accountIndex));
            }
            account.pageName = lines[lineIndex];
            this.$set(this.accountsToProcess, accountIndex, account);
          }
          if (lineIndex < lines.length - 1) {
            lineIndex++;
          } else {
            lineIndex = 0;
          }
        }
      } else {
        this.accountWithSpintax = [];
        if (lines[0] !== '' && lines[0].search(/\{[^\\{\\}]*\}/) > -1) {
          let spintaxTexts = [];
          let data = {
            text: window.btoa(unescape(encodeURIComponent(lines[0])))
          };
          for (let i = 0; i < this.accountsToProcess.length; i++) {
            let response = await this.api.post('/spintax', data);
            await spintaxTexts.push(response.data.data);
          }
          for (let accountIndex in this.accountsToProcess) {
            this.accountWithSpintax.push(parseInt(accountIndex));
            this.$set(this.accountsToProcess[accountIndex], 'pageName', spintaxTexts[accountIndex]);
          }
        } else {
          this.accountWithSpintax = [];
          for (let accountIndex in this.accountsToProcess) {
            this.$set(this.accountsToProcess[accountIndex], 'pageName', lines[0]);
          }
        }
      }
    },

    async spinOrGenerate(accountIndex) {
      if (this.accountWithSpintax.indexOf(accountIndex) > -1) {
        const lines = this.multiple.name.split('\n');
        let lineIndex = accountIndex % lines.length;
        let spintaxText = '';
        let data = {
          text: window.btoa(unescape(encodeURIComponent(lines[lineIndex])))
        };
        let response = await this.api.post('/spintax', data);
        spintaxText = await response.data.data;
        this.$set(this.accountsToProcess[accountIndex], 'pageName', spintaxText);
      } else {
        this.$set(this.accountsToProcess[accountIndex], 'pageName', ucfirst(generate().spaced));
      }
      this.$forceUpdate();
    },

    async handleSingleAvatarUpload(accountIndex) {
      let formData = new FormData();
      formData.append('file', this.$refs[`file${accountIndex}`][0].files[0]);
      const response = await this.api.post( `${this.baseUrl}/new/upload_media/fp_icons`,
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
      );

      let account = this.accountsToProcess[accountIndex];

      if (
        typeof response.data.path !== 'undefined'
      ) {
        account.uploaded_icon = response.data.path;
      } else {
        account.uploaded_icon = false;
      }
      this.$set(this.accountsToProcess, accountIndex, account);
    },

    openFileSelect(accountIndex) {
      document.getElementById(`file${accountIndex}`).click();
    },

    async loadIcons() {
      const response = await this.api(`/accounts/get_icons/?limit=${this.accounts.selected.length}`).catch(() => true);
      if (typeof response.data !== 'undefined' && typeof response.data.success !== 'undefined' && response.data.success) {
        this.icons = response.data.data;
      }
    },

    async changeIcon(accountIndex) {
      const response = await this.api.post('/accounts/get_icons/?limit=1', {exclude: this.icons});
      if (typeof response.data.success !== 'undefined' && response.data.success) {
        let account = this.accountsToProcess[accountIndex];
        account.icon = response.data.data[0];
        account.uploaded_icon = false;
        this.$set(this.accountsToProcess, accountIndex, account);
      }
    },

    async createFanPages() {
      if (!this.accountsToProcess || this.accountsToProcess.length === 0) {
        this.$store.dispatch('accounts_v2/closeDialog', 'createFanPage');
        return;
      }
      this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'accounts', value: []});

      if (this.accountsToProcess && this.accountsToProcess.length > 0) {
        const data = this.accountsToProcess.map(account => {
          return {
            account_id: account.id,
            page_name: account.pageName,
            page_category_id: account.pageCategoryId,
            icon: account.uploaded_icon ? account.uploaded_icon : account.icon,
            turnOffNotifications: this.multiple.turnOffNotifications
          };
        });
        const response = await this.api.post('/q/create_fp', {data: data});

        const fanPageJobIds = response.data.jobs || [];

        const accountsWithJob = this.accountsToProcess.map((item, index) => {
          return { 'accountId': item.id, 'jobId': fanPageJobIds[index], 'response': null, 'status': false };
        });

        await this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'accounts', value: accountsWithJob});

        if (fanPageJobIds && fanPageJobIds.length > 0) {
          const interval = setInterval(async () => {
            if (!this.dialogs.createFanPage) {
              clearInterval(interval);
            }
            const response2 = await this.api.post('/q/check_status', {ids: fanPageJobIds});
            const fanPageStatuses = response2.data.data || {};

            for (const fanPage in fanPageStatuses) {
              if (fanPageStatuses[fanPage]) {
                const accountWithJobIndex = this.createFanPageLoading.accounts.findIndex(item => parseInt(item.jobId) === parseInt(fanPage));

                if (accountWithJobIndex > -1) {
                  this.$set(this.createFanPageLoading.accounts[accountWithJobIndex], 'status', fanPageStatuses[fanPage]);
                  this.$set(this.createFanPageLoading.accounts[accountWithJobIndex], 'response', response2.data.responses[fanPage]);
                }
              }
            }
            const done = Object.values(fanPageStatuses).filter(item => item === true).length;
            await this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'done', value: done});

            if (done >= Object.keys(fanPageStatuses).length) {
              clearInterval(interval);
              this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'done', value: 0});
              this.$store.dispatch('accounts_v2/LOAD_ACCOUNTS');
            }
          }, 3000);
        }
      }
    },

    openAvatarFileSelect() {
      document.getElementById('upload-avatars').click();
    },

    async handleMultipleAvatarUpload() {
      for (const file of this.$refs['upload-avatars'].files) {
        this.loadAvatars = true;
        let formData = new FormData();
        formData.append('file', file);
        const options = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        };
        const response = await this.api.post( `${this.baseUrl}/new/upload_media/fp_icons`, formData, options);
        if (
          typeof response.data.path !== 'undefined'
        ) {
          await this.localAvatars.push(response.data.path);
        }
        this.$forceUpdate();
      }
      this.loadAvatars = false;
      let avatarIndex = 0;
      for (let accountIndex = 0; accountIndex < this.accountsToProcess.length; accountIndex++) {
        if (avatarIndex < this.localAvatars.length - 1) {
          avatarIndex++;
        } else {
          avatarIndex = 0;
        }
        let account = this.accountsToProcess[accountIndex];
        account.uploaded_icon = this.localAvatars[avatarIndex];
        this.$set(this.accountsToProcess, accountIndex, account);
      }
      this.localAvatars = [];
    },

    openDetails(response) {
      this.forDetails = response;
      this.detailsDialog = true;
    },

    close () {
      this.accountsToProcess.forEach(item => {
        if (item.pageName === '') {
          this.initialAccountsToProcess.forEach(initialAccount => {
            if (item.id === initialAccount.id) initialAccount.pageName = '';
          });
        }
      });

      if (!this.$_.isEqual(this.accountsToProcess, this.initialAccountsToProcess) || this.multiple.name !== '' || this.multiple.categoryId !== null) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('accounts_v2/closeDialog', 'createFanPage');
      this.$store.dispatch('accounts_v2/setLoadingOptions', {dialog: 'createFanPageLoading', option: 'accounts', value: []});
    },

    generateFpNames () {
      this.$set(this.multiple, 'name', '');
      for (let i = 0; i < this.accountsToProcess.length; i++) {
        this.$set(this.multiple, 'name', `${this.multiple.name}${ucfirst(generate().spaced)}\n`);
      }
    }
  }

};
</script>
